import React, { useState } from "react";
import {
  Button,
  Container,
  Grid,
  Snackbar,
  Typography
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

import PrinterType from "./PrinterType/PrinterType";
import NetworkSettings from "./NetworkType/NetworkSettings";
import NetworkType from "./NetworkType/NetworkType";
import UserLevel from "./UserLevel/UserLevel";
import Update from "../Menu/MenuTabs/Update";
import Tutorial from "../Tutorials/Tutorial/Tutorial";
import { getStorage } from "../../Utility/helpers";
import NetworkSelect from "./NetworkSelect/NetworkSelect";
const useStyles = makeStyles({
  container: {
    flex: 1,
    display: "flex",
    flexDirection: "column"
  },
  welcome: {
    textAlign: "center",
    height: "100%",
    "& h2": {
      fontSize: "92px"
    },
    "& h3": {
      fontSize: "32px",
      marginTop: "16px"
    }
  }
});
const Setup = ({ handleOnboarding }) => {
  const classes = useStyles();

  const [step, setStep] = useState(null);
  const [showSnack, setShowSnack] = useState(false);
  const [snackMessage, setSnackMessage] = useState("");
  const handleSnackClose = () => {
    setShowSnack(() => false);
    setSnackMessage("");
  };

  return (
    <Container className={classes.container}>
      {step === null && (
        <Grid
          container
          direction="column"
          justify="space-around"
          alignItems="center"
          spacing={3}
          className={classes.welcome}
        >
          <Grid
            item
            container
            direction="column"
            justify="center"
            alignItems="center"
            style={{ flex: 1 }}
          >
            <Typography variant="h2">Welcome</Typography>
            <Typography variant="h3">Let's get you set up</Typography>
          </Grid>
          <Grid
            item
            container
            alignItems="flex-start"
            justify="center"
            style={{ flex: 1 }}
          >
            <Button
              variant="contained"
              size="large"
              color="secondary"
              style={{ width: "190px", height: "65px", fontSize: "18px" }}
              onClick={() => setStep(0)}
            >
              Get Started
            </Button>
          </Grid>
        </Grid>
      )}
      {step === 0 && (
        <NetworkSelect
          setStep={setStep}
          setShowSnack={setShowSnack}
          setSnackMessage={setSnackMessage}
          handleSnackClose={handleSnackClose}
        />
        // <NetworkType
        //   setStep={setStep}
        //   setShowSnack={setShowSnack}
        //   setSnackMessage={setSnackMessage}
        //   handleSnackClose={handleSnackClose}
        //   setNewType={() => {}}
        // />
      )}
      {step === 1 && (
        <Update
          autoCheck={false}
          handleOnboarding={handleOnboarding}
          firstBoot={true}
          setStep={setStep}
        />
      )}
      {step === 2 && <PrinterType setStep={setStep} />}
      {step === 3 && (
        <NetworkSettings
          setStep={setStep}
          setShowSnack={setShowSnack}
          setSnackMessage={setSnackMessage}
          handleSnackClose={handleSnackClose}
          setNewType={() => {}}
        />
      )}
      {step === 4 && (
        <UserLevel handleOnboarding={handleOnboarding} setStep={setStep} />
      )}
      {step === 5 && (
        <Tutorial
          tutorialName="bedLevelCornerCalibration"
          handleOnboarding={handleOnboarding}
          firstBoot={true}
          setStep={setStep}
          onTutorialClose={(reason) => {
            setStep(6);
          }}
        />
      )}
      {step === 6 && (
        <Tutorial
          tutorialName="loadFilament"
          handleOnboarding={handleOnboarding}
          firstBoot={true}
          setStep={setStep}
          onTutorialClose={(reason) => {
            handleOnboarding(getStorage("userMode"));
          }}
        />
      )}
      <Snackbar
        open={showSnack}
        autoHideDuration={6000}
        message={snackMessage}
        onClose={handleSnackClose}
      ></Snackbar>
    </Container>
  );
};

export default Setup;
