import React, { useState } from "react";
import { Button, Grid, CircularProgress, Typography } from "@material-ui/core";
import { ArrowForward } from "@material-ui/icons";
import UpdateIcon from "@material-ui/icons/Update";
import { makeStyles } from "@material-ui/core/styles";
import { useMountEffect } from "../../../Utility/useMountEffect";

const useStyles = makeStyles({
  checker: {
    flex: 2,
    "& button": {
      width: "200px",
      height: "50px",
      fontSize: "14px"
    }
  },
  updateContainer: {
    flex: 3,
    "& h3": {
      fontSize: "28px",
      textAlign: "center"
    }
  }
});

const Update = ({ autoCheck, handleOnboarding, firstBoot, setStep }) => {
  const classes = useStyles();

  const [checking, setChecking] = useState(false);
  const [text, setText] = useState("Update device");
  const [updateChecked, setUpdateChecked] = useState(false);
  const [updates, setUpdates] = useState(false);
  const [checkDisabled, setCheckDisabled] = useState(false);
  const [rebootMessage, setRebootMessage] = useState(false);

  useMountEffect(() => {
    if (autoCheck) {
      handleCheck();
    }
  });
  const handleCheck = () => {
    if (firstBoot && updateChecked) {
      setStep(2);
    } else {
      setText("Checking for updates...");
      setChecking(true);
      //make api call
      //await POST body to http://${window.location.hostname}:8990/connect
      async function checkUpdates() {
        // Default options are marked with *
        const response = await fetch(
          `http://${window.location.hostname}:2127/api/v1/update/software/check`,
          {
            method: "POST",
            headers: {
              "Content-Type": "application/json"
            }
          }
        );
        return response.json(); // parses JSON response into native JavaScript objects
      }
      checkUpdates().then((data) => {
        console.log(data);
        //after api call
        if (data.update) {
          setChecking(false);
          //if updates present
          setText(data.message);
          setUpdates(true);
          setUpdateChecked(true);
        } else {
          setChecking(false);
          setText(data.message);
          setUpdateChecked(true);
        }
      });
    }
  };
  const handleDownload = () => {
    //console.log("handleDownload state 1 of 6; Entry point");
    //await POST body to http://${window.location.hostname}:8990/connect
    async function downloadUpdates() {
      //console.log("handleDownload state 2 of 6; downloadUpdates called");
      // Default options are marked with *
      const response = await fetch(
        `http://${window.location.hostname}:2127/api/v1/update/software`,
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json"
          }
        }
      );
      return response.json(); // parses JSON response into native JavaScript objects
    }
    setChecking(true);
    setText("Downloading, please wait...");
    setUpdates(false);
    //call download api
    downloadUpdates().then((data) => {
      //console.log("handleDownload state 3 of 6; Await'd downloadUpdates");
      console.log(data);
      //after api call
      if (data.updated) {
        //console.log("handleDownload state 4 of 6; data.updated === true");
        //after api call
        setChecking(false);
        //on success
        setText(data.message);
        setRebootMessage(true);
        setCheckDisabled(true);

        //console.log(
        //  "handleDownload state 5 of6 ; Waiting 3 seconds and then calling reboot"
        //);
        setTimeout(() => {
          //console.log("handleDownload state 6 of ; Calling reboot");
          fetch(`http://${window.location.hostname}:8990/reboot`, {
            method: "POST",
            headers: {
              "Content-Type": "application/json"
            }
          });
        }, 3000);
      } else {
        console.log("handleDownload state error of 6; Entry point");
        //on failure
        setText(data.message);
        setUpdates(true);
        setCheckDisabled(false);
        setChecking(false);
      }
    });
  };
  const handleSkip = () => {
    setStep(2);
  };
  return (
    <Grid
      container
      direction="column"
      alignItems="center"
      style={{ height: "100%" }}
    >
      {!checkDisabled && firstBoot && (
        <Button
          style={{ alignSelf: "flex-end", marginTop: "15px" }}
          aria-label="skip"
          endIcon={<ArrowForward />}
          onClick={handleSkip}
        >
          Skip
        </Button>
      )}

      <Grid
        container
        direction="column"
        justify="center"
        alignItems="center"
        className={classes.updateContainer}
      >
        {checking && <CircularProgress color="secondary" />}
        <UpdateIcon
          style={{
            fontSize: 100,
            width: "100%",
            textAlign: "center",
            marginBottom: 20
          }}
        />
        {text.length > 0 && (
          <Typography variant="h3" style={{
            textAlign: "center",
          }}>
            {text}
          </Typography>
        )}
        {rebootMessage && (
          <div>
            <Typography
              variant="h3"
              style={{ marginTop: 60, textAlign: "center" }}
            >
              Rebooting printer to apply update, this will take approximately 2
              minutes.
            </Typography>
            <Typography
              variant="h3"
              style={{ marginTop: 60, textAlign: "center" }}
            >
              DO NOT TURN OFF PRINTER UNTIL AFTER IT REBOOTS!
            </Typography>
          </div>
        )}
        {updates && (
          <Button
            variant="contained"
            color="secondary"
            onClick={handleDownload}
            style={{ marginTop: "24px" }}
          >
            Download Update
          </Button>
        )}
      </Grid>
      <Grid
        container
        justify="center"
        alignItems="center"
        className={classes.checker}
      >
        {!updates && !checking && (
          <Button
            variant="contained"
            color="secondary"
            disabled={checking || checkDisabled}
            onClick={handleCheck}
          >
            {firstBoot && updateChecked ? "Next" : "Check for Updates"}
          </Button>
        )}
      </Grid>
    </Grid>
  );
};

export default Update;
