import React, { useState, useEffect } from "react";
import { withStyles } from "@material-ui/core/styles";
import { Container, Grid, Typography } from "@material-ui/core";
import { Button, LinearProgress } from "@material-ui/core";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPause, faPlay, faStop } from "@fortawesome/free-solid-svg-icons";
import { PlayArrow, Pause, Stop } from "@material-ui/icons";
import { getStorage } from "../../../Utility/helpers";
import { useMountEffect } from "../../../Utility/useMountEffect";
import TutorialThermometer from "../../Tutorials/TutorialThermometer/TutorialThermometer";
import TempContext from "../../../TempContext";

import { makeStyles } from "@material-ui/core/styles";
const useStyles = makeStyles({
  buttonContainer: {
    display: "flex",
    justifyContent: "center",
    marginTop: "10px"
  },
  textCenter: {
    textAlign: "center",
    marginTop: "10px"
  },
  camera: {
    width: "100%",
    border: "2px solid white",
    borderRadius: 5
  }
});

var toHHMMSS = (secs) => {
  if(secs) {
    var sec_num = parseInt(secs, 10)
    var hours   = Math.floor(sec_num / 3600)
    var minutes = Math.floor(sec_num / 60) % 60
    var seconds = sec_num % 60

    return [hours,minutes,seconds]
        .map(v => v < 10 ? "0" + v : v)
        .filter((v,i) => v !== "00" || i > 0)
        .join(":");
  } else {
    return "calculating...";
  }
}

function imageExists(url, callback) {
  var img = new Image();
  img.onload = function() { callback(true); };
  img.onerror = function() { callback(false); };
  img.src = url;
}

const PrintStatus = ({
  lastMessage,
  progressState,
  setDisabledNav,
  state,
  statusText,
  fileName
}) => {
  const classes = useStyles();
  const [paused, setPaused] = useState(false);
  const [camEnabled, setCamEnabled] = useState(false);
  const [value, setValue] = useState(0);
  const [elapsed, setElapsed] = useState("");
  const [remaining, setRemaining] = useState("");
  const cameraFeedUrl = `http://${window.location.hostname}:8090/?action=snapshot`;
  const cameraFallbackUrl = 'no-signal.gif';
  const [cameraUrl, setCameraUrl] = useState(cameraFallbackUrl);

  let sendGcode = function (gcode) {
    fetch(`http://${window.location.hostname}:2127/api/v1/send`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        gcode: gcode
      })
    });
  };

  useMountEffect(() => {
    imageExists(cameraUrl, function(exists) {
      if(exists) {
        setCameraUrl(cameraFeedUrl);
      } else {
        setCameraUrl(cameraFallbackUrl);
      }
    });
      setCamEnabled(() => getStorage("camEnabled"));
  });

  useEffect(() => {
    if (progressState.completion !== value) {
      setValue(progressState.completion);
      setElapsed(toHHMMSS(progressState.printTime));
      setRemaining(toHHMMSS(progressState.printTimeLeft));
      console.log(value);
    }
  }, [progressState]);

  return (
    <>
      <Container>
        <Typography
          variant="h5"
          style={{ marginTop: 35, width: "100%", textAlign: "center" }}
        >
          {statusText}: {fileName}
        </Typography>
        <div className="print-status-bar" style={{ marginTop: 25 }}>
          <BorderLinearProgress variant="determinate" value={value} />
          <Grid container spacing={3} alignItems="center">
          <Grid item xs={4}  style={{textAlign: "left"}}>
          <Typography
            variant="h6"
            component="span"
            style={{ width: "100%" }}
          >
            {elapsed}
          </Typography>
          </Grid>
          <Grid item xs={4}  style={{textAlign: "center"}}>
          <Typography
            variant="h6"
            component="span"
            style={{ width: "100%" }}
          >
            {value ? value.toFixed(1) : "0"}%
          </Typography>
          </Grid>
          <Grid item xs={4} style={{textAlign: "right"}}>
          <Typography
            variant="h6"
            component="span"
            style={{ width: "100%" }}
          >
            {remaining}
          </Typography>
          </Grid>
          </Grid>
        </div>
        <div>
        <Grid container alignItems="center">
        <Grid item xs={getStorage("camEnabled") ? 6 : 12} alignItems="center">
          <TempContext.Consumer>
            {({ bed, nozzle, bedTarget, nozzleTarget }) => (
              <Grid container alignItems="center" style={{ justifyContent: "space-around" }}>
                <Grid item container xs={3} direction="column">
                  <Grid item style={{ marginBottom: -100 }}>
                    <div style={{ transform: "scale(.6)" }}>
                      <TutorialThermometer min={0} max={120} value={bed} />
                    </div>
                  </Grid>
                  <Grid item style={{ textAlign: "center" }}>
                    <Typography variant="h6">
                      Target: {bedTarget}
                    </Typography>
                    <Typography variant="h6">
                      Bed
                    </Typography>
                    <div className="print-btn-container">
                      <Button
                        color="primary"
                        variant="contained"
                        onClick={() => {
                          setPaused((prevState) => !prevState);
                          fetch(`http://${window.location.hostname}:2127/api/v1/job`, {
                            method: "PATCH",
                            headers: { "Content-Type": "application/json" },
                            body: JSON.stringify({
                              file: state.files[state.file]
                            })
                          });
                        }}
                        startIcon={!paused ? <Pause /> : <PlayArrow />}
                      >
                        {!paused ? "Pause" : "Play"}
                      </Button>
                    </div>
                  </Grid>
                </Grid>
                <Grid item container xs={3} direction="column">
                  <Grid item style={{ marginBottom: -100 }}>
                    <div style={{ transform: "scale(.6)" }}>
                      <TutorialThermometer min={0} max={300} value={nozzle} />
                    </div>
                  </Grid>
                  <Grid item style={{ textAlign: "center" }}>
                    <Typography variant="h6">
                      Target: {nozzleTarget}
                    </Typography>
                    <Typography variant="h6">
                      Nozzle
                    </Typography>
                    <div className="print-btn-container">
                      <Button
                        color="secondary"
                        variant="contained"
                        onClick={() => {
                          fetch(`http://${window.location.hostname}:2127/api/v1/job`, {
                            method: "DELETE",
                            headers: { "Content-Type": "application/json" },
                            body: JSON.stringify({
                              file: state.files[state.file]
                            })
                          });
                        }}
                        startIcon={<Stop />}
                      >
                        Stop
                      </Button>
                    </div>
                  </Grid>
                </Grid>
              </Grid>
            )}
          </TempContext.Consumer>
        </Grid>
        {camEnabled === 'enabled' && (
          <Grid item xs={6} style={{ display: camEnabled ? 'block' : 'none' }}>
            <img src={cameraUrl} className={classes.camera} />
          </Grid>
        )}
        </Grid>
        </div>
      </Container>
    </>
  );
};

const BorderLinearProgress = withStyles((theme) => ({
  root: {
    height: 14,
    borderRadius: 7
  },
  colorPrimary: {
    backgroundColor: "#606060"
  },
  bar: {
    borderRadius: 7,
    backgroundColor: "#ACC12F"
  }
}))(LinearProgress);

export default PrintStatus;
