import React from "react";
import {
  AppBar,
  Card,
  CardContent,
  Container,
  Typography
} from "@material-ui/core";
import TutorialDefinitions from "../../Tutorials/TutorialDefinitions";
import Tutorial from "../../Tutorials/Tutorial/Tutorial";
import { withStyles } from "@material-ui/core/styles";

const styles = (theme) => ({
  tutorialContainer: {
    maxHeight: "950px",
    overflow: "auto"
  },
  tutorialCard: {
    backgroundColor: "rgb(46,46,46)",
    marginBottom: "8px"
  },
  updateContainer: {
    flex: 3,
    "& h3": {
      fontSize: "28px",
      textAlign: "center"
    }
  }
});
class Tutorials extends React.Component {
  constructor(props) {
    super(props);

    this.state = { currentTutorial: null };
  }
  selectTutorial(id) {
    this.setState({ currentTutorial: id });
  }
  closeTutorial(reason) {
    this.setState({ currentTutorial: null });
    if (reason && reason === "request-calibration") {
      setTimeout(() => {
        this.selectTutorial("bedLevelSensorCalibration");
      }, 1000);
    }
  }
  render() {
    const { classes } = this.props;
    return (
      <>
        <Container className={classes.tutorialContainer}>
          {Object.getOwnPropertyNames(TutorialDefinitions).map((tutName) => {
            if (TutorialDefinitions[tutName].userVisable) {
              return (
                <>
                  <Card
                    className={classes.tutorialCard}
                    onClick={() => {
                      this.selectTutorial(tutName);
                    }}
                  >
                    <CardContent>
                      <Typography variant="h6">
                        {TutorialDefinitions[tutName].name}
                      </Typography>
                      <Typography variant="body2">
                        {TutorialDefinitions[tutName].description}
                      </Typography>
                    </CardContent>
                  </Card>
                </>
              );
            } else {
              return <></>;
            }
          })}
        </Container>
        {this.state.currentTutorial && (
          <Tutorial
            tutorialName={this.state.currentTutorial}
            showClose={true}
            onTutorialClose={(reason) => {
              this.closeTutorial(reason);
            }}
          ></Tutorial>
        )}
      </>
    );
  }
}

export default withStyles(styles)(Tutorials);
