import React, { useState } from "react";
import {
  AppBar,
  Container,
  Divider,
  Drawer,
  IconButton,
  List,
  ListItem,
  ListItemText,
  Typography
} from "@material-ui/core";
import ArrowForwardIosIcon from "@material-ui/icons/ArrowForwardIos";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import { makeStyles } from "@material-ui/core/styles";
import Update from "./MenuTabs/Update";
import ManageStorage from "./MenuTabs/ManageStorage";
import About from "./MenuTabs/About";
import Settings from "./MenuTabs/Settings";
import Tutorials from "./MenuTabs/Tutorials";
import ResetPrinter from "./MenuTabs/ResetPrinter";

const useStyles = makeStyles({
  componentContainer: {
    display: "flex",
    flex: 1
  },
  drawerContainer: {
    width: "100%",
    maxWidth: "700px"
  },
  header: {
    display: "flex",
    alignItems: "center"
  },
  item: {
    height: "100px",
    padding: "0 22px"
  },
  text: {
    fontSize: "1.5em"
  }
});

const Menu = (lastMessage) => {
  const classes = useStyles();
  const [drawerOpen, setDrawerOpen] = useState(false);
  const [activeComponent, setActiveComponent] = useState({});

  const handleDrawer = (item) => {
    //call setActiveComponent
    setActiveComponent(item);
    setDrawerOpen(true);
  };
  const MenuArray = [
    {
      name: "Tutorials",
      component: <Tutorials />
    },
    {
      name: "Settings",
      component: <Settings />
    },
    {
      name: "Storage",
      component: <ManageStorage />
    },
    //{ name: "Troubleshooting", component: null },
    { name: "Update", component: <Update firstBoot={false} /> },
    { name: "Reset", component: <ResetPrinter /> },
    { name: "About", component: <About /> }

  ];
  return (
    <div>
      <List>
        {MenuArray.map((item) => {
          return (
            <React.Fragment key={`${item.name}-item`}>
              <ListItem
                button
                onClick={() => handleDrawer(item)}
                className={classes.item}
              >
                <ListItemText
                  primary={
                    <Typography className={classes.text}>
                      {item.name}
                    </Typography>
                  }
                />
                <ArrowForwardIosIcon />
              </ListItem>
              <Divider />
            </React.Fragment>
          );
        })}
      </List>
      <Drawer
        anchor="right"
        open={drawerOpen}
        onClose={() => setDrawerOpen(false)}
        classes={{
          paper: classes.drawerContainer
        }}
      >
        <AppBar position="sticky">
          <Typography variant="h6" style={{ textAlign: "center" }}>
            {window.location.hostname}
          </Typography>
        </AppBar>
        <div className={classes.header}>
          <IconButton aria-label="back" onClick={() => setDrawerOpen(false)}>
            <ArrowBackIcon />
          </IconButton>
          <Typography variant="h5">
            {activeComponent.name}
          </Typography>
        </div>
        <Container className={classes.componentContainer}>
          {activeComponent.component}
        </Container>
      </Drawer>
    </div>
  );
};

export default Menu;
