import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Grid, Button, Typography } from "@material-ui/core";
import { ArrowForward } from "@material-ui/icons";
import { setStorage, removeStorage } from "../../../Utility/helpers";
import OptionCard from "../../Common/OptionCard/OptionCard";
import WifiConnect from "./WifiConnect";
import HotspotConnect from "./HotspotConnect";

const useStyles = makeStyles({
  header: {
    display: "flex",
    "& h2": {
      fontSize: "28px"
    }
  }
});
const NetworkType = ({
  setStep,
  setSnackMessage,
  setShowSnack,
  handleSnackClose,
  setNewType
}) => {
  const classes = useStyles();
  const [connectWifi, setConnectWifi] = useState(null);
  const [flowStep, setFlowStep] = useState("choose-overall");
  const [hotspotFollow, setHotspotFollow] = useState(null);

  const handleClick = (val) => {
    setStorage("networkProfile", val);
    setConnectWifi(val);
    setNewType(val);
    if (val === "hotspot") {
      fetch(`http://${window.location.hostname}:8990/hotspot/connect`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        }
      });
    } else if (val === "wifi") {
      fetch(`http://${window.location.hostname}:8990/hotspot/disconnect`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        }
      });
    } else if (val === "none") {
      setStep(1);
    }
  };
  return (
    <>
      {connectWifi === null ? (
        <>
          <Grid
            container
            justify="space-between"
            alignItems="center"
            className={classes.header}
          >
            <h2>Network</h2>
            <Button
              aria-label="skip"
              endIcon={<ArrowForward />}
              onClick={() => {
                removeStorage("networkProfile");
                setStep(1);
              }}
            >
              Skip
            </Button>
          </Grid>
          {flowStep === "choose-overall" && (
            <>
              <Typography variant="h5">
                Do you want to connect this printer to the internet?
              </Typography>
              <OptionCard
                title="Yes"
                onClick={() => {
                  setStorage("internet", true);
                  setFlowStep("choose-internet");
                }}
              />
              <OptionCard
                title="No"
                onClick={() => {
                  setStorage("internet", false);
                  setFlowStep("choose-hotspot");
                }}
              />
            </>
          )}
          {flowStep === "choose-internet" && (
            <>
              <Typography variant="h5">
                How will this printer connect to the internet?
              </Typography>
              <OptionCard
                title="Wi-Fi"
                onClick={() => {
                  handleClick("wifi");
                }}
              />
              <OptionCard
                title="Ethernet"
                onClick={() => {
                  handleClick("ethernet");
                }}
              />
            </>
          )}
          {flowStep === "choose-hotspot" && (
            <>
              <Typography variant="h5">
                Would you like to activate the printer's wireless hotspot?
              </Typography>
              <Typography variant="h6">
                This allows you to remotely access and control the printer.
              </Typography>
              <OptionCard
                title="Yes"
                onClick={() => {
                  setStorage("hotspot", true);
                  handleClick("hotspot");
                }}
              />
              <OptionCard
                title="No"
                onClick={() => {
                  setStorage("hotspot", false);
                }}
              />
            </>
          )}
          <OptionCard title="Wi-Fi" onClick={() => handleClick("wifi")} />
          <OptionCard title="Hotspot" onClick={() => handleClick("hotspot")} />
          <OptionCard
            title="Ethernet"
            onClick={() => handleClick("ethernet")}
          />
          <OptionCard title="None" onClick={() => handleClick("none")} />
        </>
      ) : connectWifi === "wifi" ? (
        <WifiConnect
          setStep={setStep}
          setShowSnack={setShowSnack}
          setSnackMessage={setSnackMessage}
          handleSnackClose={handleSnackClose}
          setConnectWifi={setConnectWifi}
        />
      ) : (
        <HotspotConnect
          setStep={setStep}
          setConnectWifi={setConnectWifi}
          type={connectWifi}
        />
      )}
    </>
  );
};

export default NetworkType;
