import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import TutorialButton from "../Tutorials/Tutorial/TutorialButton";

const Tune = (lastMessage) => {
  const [offset, setOffset] = useState(0);
  const units = [0.01, 0.05, 0.1, 1.0, 10];
  const [unit, setUnit] = useState(1);
  const [extruder, setExtruder] = useState(0);
  const [bed, setBed] = useState(0);

  React.useEffect(() => {
    if (
      typeof lastMessage !== "undefined" &&
      lastMessage &&
      typeof lastMessage.data !== "undefined" &&
      lastMessage.data.length > 1
    ) {
      let json = JSON.parse(lastMessage.data.substring(1));
      console.log(json);
      if (json[0].hasOwnProperty("current") && json[0].current.temps[0]) {
        if (json[0].current.temps[0]) {
          let temps = json[0].current.temps[0];
          setExtruder(parseInt(temps.tool0.actual));
          setBed(parseInt(temps.bed.actual));
        }
      }
    }
  }, [lastMessage]);

  const useStyles = makeStyles({
    btnContainer: {
      marginTop: "14px",
      marginBottom: "28px",
      textAlign: "center"
    },
    centerBtns: {
      "& button": {
        margin: "0 12px"
      }
    },
    tabs: {
      "& button": {
        textTransform: "none"
      }
    }
  });
  const classes = useStyles();

  let sendGcode = function (gcode) {
    fetch(`http://${window.location.hostname}:2127/api/v1/send`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        gcode: gcode
      })
    });
  };
  let position = { x: 0, y: 0, z: 0 };

  return (
    <div>
      <TutorialButton
        tutorialName={"blTouchCalibration"}
        extruderTemp={extruder}
        bedTemp={bed}
      ></TutorialButton>
      <TutorialButton
        tutorialName={"bedLevelCornerCalibration"}
        extruderTemp={extruder}
        bedTemp={bed}
      ></TutorialButton>
      {/*<Button
        color="primary"
        variant="contained"
        onClick={() => {
          sendGcode("G29 T\nM114\n");
        }}
      >
        Generate Mesh
      </Button>*/}
      <TutorialButton
        tutorialName={"loadFilament"}
        extruderTemp={extruder}
        bedTemp={bed}
      ></TutorialButton>
      <TutorialButton
        tutorialName={"unloadFilament"}
        extruderTemp={extruder}
        bedTemp={bed}
      ></TutorialButton>
    </div>
  );
};

export default Tune;
