import {
  Grid,
  Paper,
  Typography,
  Slider,
  Button,
  IconButton
} from "@material-ui/core";
import {
  ArrowDownward,
  ArrowUpward,
  RotateLeft,
  RotateRight,
  Videocam,
  ZoomIn,
  ZoomOut
} from "@material-ui/icons";
import React from "react";

import AppContext from "../../../../AppContext";

import "./style.css";

function NavigationPanel() {
  //   const [zoom, setZoom] = React.useState(50);
  return (
    <>
      <div className="navigationpanel">
        <Paper
          style={{
            width: "100%",
            height: "100%",
            textAlign: "center",
            paddingLeft: 8,
            paddingRight: 8
          }}
        >
          <Videocam
            style={{ color: "rgb(100,100,100)", height: 50, width: 40 }}
          />
          <AppContext.Consumer>
            {({ camZoom, setZoom, addRotation, addZ }) => (
              <>
                <Grid container spacing={2}>
                  <Grid item>
                    <ZoomOut />
                  </Grid>
                  <Grid item xs>
                    <Slider
                      value={camZoom}
                      onChange={(e, newValue) => {
                        setZoom(newValue);
                      }}
                    />
                  </Grid>
                  <Grid item>
                    <ZoomIn />
                  </Grid>
                </Grid>
                <Grid container>
                  <Grid item container direction="column" xs={3}></Grid>
                  <Grid
                    item
                    container
                    direction="column"
                    justify="center"
                    xs={2}
                  >
                    <Grid item>
                      <IconButton
                        aria-label="Rotate camera left"
                        onClick={() => {
                          addRotation(-15);
                        }}
                      >
                        <RotateRight />
                      </IconButton>
                    </Grid>
                  </Grid>
                  <Grid item container direction="column" xs={2}>
                    <Grid item>
                      <IconButton
                        aria-label="move camera up"
                        onClick={() => {
                          addZ(10);
                        }}
                      >
                        <ArrowUpward />
                      </IconButton>
                    </Grid>
                    <Grid item>
                      <IconButton></IconButton>
                    </Grid>
                    <Grid item>
                      <IconButton
                        aria-label="move camera down"
                        onClick={() => {
                          addZ(-10);
                        }}
                      >
                        <ArrowDownward />
                      </IconButton>
                    </Grid>
                  </Grid>
                  <Grid
                    item
                    container
                    direction="column"
                    justify="center"
                    xs={2}
                  >
                    <Grid item>
                      <IconButton
                        aria-label="rotate camera right"
                        onClick={() => {
                          addRotation(15);
                        }}
                      >
                        <RotateLeft />
                      </IconButton>
                    </Grid>
                  </Grid>
                  <Grid item container direction="column" xs={3}></Grid>
                </Grid>
              </>
            )}
          </AppContext.Consumer>
        </Paper>
      </div>
    </>
  );
}

export default NavigationPanel;
