import React from "react";
import "./style.css";

function TutorialThermometer(props) {
  return (
    <div className="big-thermometer">
      <div className="big-thermometer-container">
        <div className="big-thermometer-bulb-container"></div>
        <div className="big-thermometer-indicator-container">
          <div
            className="big-thermometer-indicator"
            style={{
              height: `${
                15 + 85 * ((props.value - props.min) / (props.max - props.min))
              }%`
            }}
          ></div>
          <div className="big-thermometer-bulb">
            <span>{props.value}</span>
          </div>
        </div>
      </div>
    </div>
  );
}

export default TutorialThermometer;
