import React from "react";
import { withStyles } from "@material-ui/core/styles";
import TutorialDefinitions from "../TutorialDefinitions";
import TutorialLayouts from "../TutorialLayouts";
import {
  Button,
  Container,
  Grid,
  IconButton,
  Typography,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  DialogContentText,
  AppBar
} from "@material-ui/core";
import { ArrowDownward, ArrowUpward, CloseSharp } from "@material-ui/icons";
import TutorialThermometer from "../TutorialThermometer/TutorialThermometer";
import {
  GCodeBuilder,
  GSetZProbeOffset,
  GStoreParams,
  GLoadParams,
  GMoveToOrigin,
  GSetToAbsolutePositioning,
  GSetSoftwareEndstops,
  GLinearMove,
  GSetExtruderTemp,
  GSetBedTemp,
  Home,
  BlLevel
} from "../GCodeBuilder";

import TempContext from "../../../TempContext";

const styles = (theme) => ({
  tutorialWindow: {
    position: "fixed",
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    zIndex: 1250,
    backgroundColor: "#303030"
  },
  tutorialButton: {
    // backgroundColor: "rgb(42, 57, 131)",
    fontSize: "20px"
  },
  tutorialCloseButton: {
    float: "right",
    marginRight: -8
  },
  tutorialText: {
    fontSize: "20px"
  },
  tutorialContainer: {
    padding: "8px",
    height: "calc(100% - 48px)"
  },
  tutorialHeading: {
    paddingTop: "8px"
  },
  layoutSingleGrid: {
    paddingLeft: "16px",
    paddingRight: "16px"
  },
  layoutSingleGridText: {
    // height: "50%"
    flexGrow: 1
  },
  layoutSingleGridButton: {
    height: "80px",
    textAlign: "center"
  },
  layoutSingleGridTop: {
    height: "50px"
  },
  layoutSingleGridAxisControl: {
    height: "550px"
  },
  layoutSingleGridThermalControl: {
    height: "550px"
  },
  axisButton: {
    marginTop: "2px",
    marginBottom: "2px",
    fontSize: "20px",
    textTransform: "none",
    width: "120px"
  },
  thermoLabel: {
    textAlign: "center",
    fontSize: "26px"
  }
});

class Tutorial extends React.Component {
  constructor(props) {
    super(props);
    this.evTutorialClose =
      props.onTutorialClose ||
      (() => {
        console.log("Unhandled tutorial close event");
      });
    this.state = {
      tutorialId: this.props.tutorialName || null,
      tutorialReady: false,
      currentStep: 0,
      exitWarning: false
    };
    this.tutorial = null;
    this.offset = 0;
  }

  static contextType = TempContext;

  componentDidMount() {
    if (this.state.tutorialId && TutorialDefinitions[this.state.tutorialId]) {
      this.tutorial = TutorialDefinitions[this.state.tutorialId];
      this.setState({ tutorialReady: true, currentStep: 0 });
    } else {
      console.log(
        `Unknown tutorial id "${this.state.tutorialId}", closing tutorial`
      );
      this.evTutorialClose();
    }
  }

  componentDidUpdate(prevProps) {
    if (this.tutorial) {
      const currentStep = this.tutorial.steps[this.state.currentStep];
      if (currentStep.layout === TutorialLayouts.ThermalGate) {
        //We only need to trigger ThermalGates when they are displayed
      }
    }
  }

  attemptExitActions() {
    this.processButtonActions(this.tutorial.exitActions);
  }

  attemptCloseTutorial(reason) {
    if (
      this.state.currentStep === 0 ||
      this.currentStep === this.tutorial.steps.length - 1
    ) {
      //This is the first or last step of the tutorial, so we can just close without any warnings
      this.evTutorialClose(reason);
    } else {
      //The tutorial is in-progress, confirm that the user really wishes to exit
      this.setState({ exitWarning: true });
    }
  }

  attemptAxisMove(axis, amount) {
    console.log(`Attempting to move on the ${axis} axis by ${amount}mm`);
    if (axis.toLowerCase() === "z") {
      this.offset += amount;
      let codeBuilder = new GCodeBuilder();
      codeBuilder.addCommand(GSetToAbsolutePositioning());
      codeBuilder.addCommand(
        GLinearMove(false, false, true, null, null, this.offset)
      );
      this.sendGcode(codeBuilder.build());
    }
  }

  attemptHome() {
    console.log(`Attempting to home`);
    let codeBuilder = new GCodeBuilder();
    codeBuilder.addCommand(Home());
    this.sendGcode(codeBuilder.build());
  }

  attemptBlLevel() {
    console.log(`Attempting to BL Level`);
    let codeBuilder = new GCodeBuilder();
    codeBuilder.addCommand(BlLevel());
    this.sendGcode(codeBuilder.build());
  }

  attemptXYMoveTo(x, y) {
    console.log(`Attempting to move print head to x:${x}, y: ${y}`);
    let codeBuilder = new GCodeBuilder();
    codeBuilder.addCommand(GSetToAbsolutePositioning());
    codeBuilder.addCommand(GLinearMove(true, true, true, 150, 150, 30));
    codeBuilder.addCommand(GLinearMove(true, true, true, x, y, 0));
    this.sendGcode(codeBuilder.build());
  }

  attemptZOffsetStart() {
    console.log(`Attempting to start z offset process`);
    let codeBuilder = new GCodeBuilder();
    codeBuilder.addCommand(GSetZProbeOffset(0.0));
    codeBuilder.addCommand(GStoreParams());
    codeBuilder.addCommand(GLoadParams());
    codeBuilder.addCommand(GMoveToOrigin(true, true, true));
    codeBuilder.addCommand(GSetToAbsolutePositioning());
    codeBuilder.addCommand(GSetSoftwareEndstops(false));
    codeBuilder.addCommand(GLinearMove(true, true, true, 150, 150, 0.0));
    this.sendGcode(codeBuilder.build());
    this.offset = 0;
  }

  attemptSaveZOffset() {
    console.log(`Attempting to save z offset`);
    let codeBuilder = new GCodeBuilder();
    codeBuilder.addCommand(GSetToAbsolutePositioning());
    codeBuilder.addCommand(GSetZProbeOffset(this.offset));
    codeBuilder.addCommand(GSetSoftwareEndstops(true));
    codeBuilder.addCommand(GStoreParams());
    codeBuilder.addCommand(GLoadParams());
    codeBuilder.addCommand(GMoveToOrigin(true, true, true));
    this.sendGcode(codeBuilder.build());
  }

  attemptSetBedTemp(temp) {
    console.log(`Attempting to set bed temperature to ${temp} C`);
    this.context.setBedTarget(temp);
    let codeBuilder = new GCodeBuilder();
    codeBuilder.addCommand(GSetBedTemp(temp));
    this.sendGcode(codeBuilder.build());
  }

  attemptSetExtruderTemp(temp) {
    console.log(`Attempting to set extruder temperature to ${temp} C`);
    this.context.setNozzleTarget(temp);
    let codeBuilder = new GCodeBuilder();
    codeBuilder.addCommand(GSetExtruderTemp(temp));
    this.sendGcode(codeBuilder.build());
  }

  attemptTestPrint() {
    fetch(
      `http://${window.location.hostname}:2127/api/v1/job/BLTouchCalibrationTest.gcode`,
      {
        method: "POST",
        headers: { "Content-Type": "application/json" }
      }
    );
  }

  tutorialButtonPressed(id) {
    const currentStep = this.tutorial.steps[this.state.currentStep];
    if (
      currentStep.layout === TutorialLayouts.ButtonSingle ||
      currentStep.layout === TutorialLayouts.AxisManipulation ||
      currentStep.layout === TutorialLayouts.ThermalGate ||
      currentStep.layout === TutorialLayouts.DualThermalGate
    ) {
      this.processButtonActions(currentStep.buttonActions);
    } else if (currentStep.layout === TutorialLayouts.ButtonMulti) {
      this.processButtonActions(
        currentStep.buttons.find((b) => {
          return b.id === id;
        }).buttonActions
      );
    } else {
      //Do correct button selection logic
    }
  }

  sendGcode(gcode) {
    fetch(`http://${window.location.hostname}:2127/api/v1/send`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        gcode: gcode
      })
    });
  }

  processButtonActions(actions) {
    for (const action of actions) {
      switch (action.type) {
        case "continue":
          this.actionContinue();
          break;
        case "home":
          //Attempt to move the extruder on the x and y axis
          this.attemptHome();
          break;
        case "bl_level":
          //Attempt to move the extruder on the x and y axis
          this.attemptBlLevel();
          break;
        case "movexy":
          //Attempt to move the extruder on the x and y axis
          this.attemptXYMoveTo(action.params[0], action.params[1]);
          break;
        case "jumpto":
          this.actionJumpTo(action.params[0]);
          break;
        case "exit":
          this.evTutorialClose(
            action.params.length > 0 ? action.params[0] : ""
          );
          break;
        case "savezoffset":
          //Attempt to set the z offset from the current z position
          this.attemptSaveZOffset();
          break;
        case "nozzletemp":
          //Attempt to set extruder temp
          this.attemptSetExtruderTemp(action.params[0]);
          break;
        case "bedtemp":
          //Attempt to set bed temperature
          this.attemptSetBedTemp(action.params[0]);
          break;
        case "printtest":
          //Attempt to print test article
          this.attemptTestPrint();
          break;
        case "zoffsetstart":
          //Zero out the printer to start zoffset
          this.attemptZOffsetStart();
          break;
        default:
          console.log(
            `Warning! Unhandled tutorial action type: "${action.type}"`
          );
      }
    }
  }

  /**
   * Attempts to continue to the next tutorial step
   */
  actionContinue() {
    if (this.state.currentStep < this.tutorial.steps.length - 1) {
      this.setState({ currentStep: this.state.currentStep + 1 });
    }
  }

  /**
   * Jumps the tutorial back (or forward) to a specific step
   * @param {Number} step The index of the step
   */
  actionJumpTo(step) {
    this.setState({ currentStep: step });
  }

  render() {
    const { classes } = this.props;
    return (
      <div className={classes.tutorialWindow}>
        <AppBar position="sticky">
          <Typography variant="h6" style={{ paddingLeft: 8 }}>
            Printer Name: {window.location.hostname}
          </Typography>
        </AppBar>
        {this.state.tutorialReady && (
          <Container className={classes.tutorialContainer}>
            <Grid container>
              <Grid item xs={10}>
                <Typography variant="h5" className={classes.tutorialHeading}>
                  {this.tutorial.name}
                </Typography>
              </Grid>
              <Grid item xs={2}>
                <IconButton
                  className={classes.tutorialCloseButton}
                  onClick={() => {
                    this.attemptCloseTutorial(
                      this.props.firstBoot ? "skip" : null
                    );
                  }}
                >
                  <CloseSharp />
                </IconButton>
              </Grid>
            </Grid>
            {this.renderTutorialPage(classes)}
          </Container>
        )}
        <Dialog open={this.state.exitWarning}>
          <DialogTitle>Exit Tutorial</DialogTitle>
          <DialogContent>
            <DialogContentText>
              Are you sure you want to exit this tutorial?
            </DialogContentText>
            <DialogActions>
              <Button
                onClick={() => {
                  this.attemptExitActions();
                  this.evTutorialClose();
                }}
              >
                Yes
              </Button>
              <Button
                autoFocus
                onClick={() => {
                  this.setState({ exitWarning: false });
                }}
              >
                No
              </Button>
            </DialogActions>
          </DialogContent>
        </Dialog>
      </div>
    );
  }

  renderTutorialPage(classes) {
    const currentStep = this.tutorial.steps[this.state.currentStep];

    if (currentStep.layout === TutorialLayouts.ButtonSingle) {
      return (
        <Grid
          container
          direction="column"
          alignItems="stretch"
          alignContent="stretch"
          justify="space-between"
          className={classes.layoutSingleGrid}
        >
          <Grid item className={classes.layoutSingleGridText}>
            {currentStep.text.split("%BREAK%").map((textSegment) => {
              return (
                <Typography variant="body1" className={classes.tutorialText}>
                  {textSegment}
                </Typography>
              );
            })}
          </Grid>
          <Grid item className={classes.layoutSingleGridButton}>
            <Button
              color="primary"
              variant="contained"
              className={classes.tutorialButton}
              onClick={() => {
                this.tutorialButtonPressed();
              }}
            >
              {currentStep.buttonText}
            </Button>
          </Grid>
        </Grid>
      );
    } else if (currentStep.layout === TutorialLayouts.AxisManipulation) {
      return (
        <Grid
          container
          direction="column"
          alignItems="stretch"
          alignContent="stretch"
          justify="space-between"
          className={classes.layoutSingleGrid}
        >
          <Grid item className={classes.layoutSingleGridText}>
            {currentStep.text.split("%BREAK%").map((textSegment) => {
              return (
                <Typography variant="body1" className={classes.tutorialText}>
                  {textSegment}
                </Typography>
              );
            })}
          </Grid>
          <Grid item className={classes.layoutSingleGridAxisControl}>
            <Grid container justify="center">
              {currentStep.axisX && this.renderAxisControl("X", classes)}
              {currentStep.axisY && this.renderAxisControl("Y", classes)}
              {currentStep.axisZ && this.renderAxisControl("Z", classes)}
            </Grid>
          </Grid>
          <Grid item className={classes.layoutSingleGridButton}>
            <Button
              color="primary"
              variant="contained"
              className={classes.tutorialButton}
              onClick={() => {
                this.tutorialButtonPressed();
              }}
            >
              {currentStep.buttonText}
            </Button>
          </Grid>
        </Grid>
      );
    } else if (currentStep.layout === TutorialLayouts.ButtonMulti) {
      return (
        <Grid
          container
          direction="column"
          alignItems="stretch"
          alignContent="stretch"
          justify="space-between"
          className={classes.layoutSingleGrid}
        >
          <Grid item className={classes.layoutSingleGridText}>
            {currentStep.text.split("%BREAK%").map((textSegment) => {
              return (
                <Typography variant="body1" className={classes.tutorialText}>
                  {textSegment}
                </Typography>
              );
            })}
          </Grid>
          <Grid item className={classes.layoutSingleGridButton}>
            <Grid container justify="space-around">
              {currentStep.buttons.map((button) => {
                return (
                  <Grid item xs={6}>
                    <Button
                      color="primary"
                      variant="contained"
                      className={classes.tutorialButton}
                      onClick={() => {
                        this.tutorialButtonPressed(button.id);
                      }}
                      id={button.id}
                    >
                      {button.buttonText}
                    </Button>
                  </Grid>
                );
              })}
            </Grid>
          </Grid>
        </Grid>
      );
    } else if (currentStep.layout === TutorialLayouts.ThermalGate) {
      return (
        <Grid
          container
          direction="column"
          alignItems="stretch"
          alignContent="stretch"
          justify="space-between"
          className={classes.layoutSingleGrid}
        >
          <Grid item className={classes.layoutSingleGridText}>
            {this.context.nozzle >= currentStep.minimumTemp
              ? currentStep.gateText.split("%BREAK%").map((textSegment) => {
                  return (
                    <Typography
                      variant="body1"
                      className={classes.tutorialText}
                    >
                      {textSegment}
                    </Typography>
                  );
                })
              : currentStep.text.split("%BREAK%").map((textSegment) => {
                  return (
                    <Typography
                      variant="body1"
                      className={classes.tutorialText}
                    >
                      {textSegment}
                    </Typography>
                  );
                })}
          </Grid>
          <Grid item className={classes.layoutSingleGridThermalControl}>
            <Grid container justify="center">
              <TutorialThermometer
                min={0}
                max={400}
                value={this.context.nozzle}
              ></TutorialThermometer>
            </Grid>
          </Grid>
          <Grid item className={classes.layoutSingleGridButton}>
            <Button
              color="primary"
              variant="contained"
              disabled={!(this.context.nozzle >= currentStep.minimumTemp)}
              className={classes.tutorialButton}
              onClick={() => {
                this.tutorialButtonPressed();
              }}
            >
              {currentStep.buttonText}
            </Button>
          </Grid>
        </Grid>
      );
    } else if (currentStep.layout === TutorialLayouts.DualThermalGate) {
      return (
        <Grid
          container
          direction="column"
          alignItems="stretch"
          alignContent="stretch"
          justify="space-between"
          className={classes.layoutSingleGrid}
        >
          <Grid item className={classes.layoutSingleGridText}>
            {this.context.nozzle >= currentStep.minimumExtruderTemp &&
            this.context.bed >= currentStep.minimumBedTemp
              ? currentStep.gateText.split("%BREAK%").map((textSegment) => {
                  return (
                    <Typography
                      variant="body1"
                      className={classes.tutorialText}
                    >
                      {textSegment}
                    </Typography>
                  );
                })
              : currentStep.text.split("%BREAK%").map((textSegment) => {
                  return (
                    <Typography
                      variant="body1"
                      className={classes.tutorialText}
                    >
                      {textSegment}
                    </Typography>
                  );
                })}
          </Grid>
          <Grid item className={classes.layoutSingleGridThermalControl}>
            <Grid container justify="center">
              <Grid item>
                <TutorialThermometer
                  min={0}
                  max={400}
                  value={this.context.bed}
                ></TutorialThermometer>
                <Typography variant="body1" className={classes.thermoLabel}>
                  Bed
                </Typography>
              </Grid>
              <Grid item>
                <TutorialThermometer
                  min={0}
                  max={400}
                  value={this.context.nozzle}
                ></TutorialThermometer>
                <Typography variant="body1" className={classes.thermoLabel}>
                  Extruder
                </Typography>
              </Grid>
            </Grid>
          </Grid>
          <Grid item className={classes.layoutSingleGridButton}>
            <Button
              color="primary"
              variant="contained"
              disabled={
                !(
                  this.context.nozzle >= currentStep.minimumExtruderTemp &&
                  this.context.bed >= currentStep.minimumBedTemp
                )
              }
              className={classes.tutorialButton}
              onClick={() => {
                this.tutorialButtonPressed();
              }}
            >
              {currentStep.buttonText}
            </Button>
          </Grid>
        </Grid>
      );
    }
  }

  renderAxisControl(axis, classes) {
    return (
      <Grid item container direction="column" alignItems="center" xs={4}>
        <Grid item>
          <IconButton disabled>
            <ArrowUpward></ArrowUpward>
          </IconButton>
          {/* <Button disabled>
            {(axis === "X" && "Left") ||
              (axis === "Y" && "Further") ||
              (axis === "Z" && "Up")}
          </Button> */}
        </Grid>
        <Grid item>
          <Button
            color="secondary"
            variant="contained"
            className={classes.axisButton}
            onClick={() => {
              this.attemptAxisMove(axis, 1.0);
            }}
          >
            +1.0mm
          </Button>
        </Grid>
        <Grid item>
          <Button
            color="secondary"
            variant="contained"
            className={classes.axisButton}
            onClick={() => {
              this.attemptAxisMove(axis, 0.1);
            }}
          >
            +0.1mm
          </Button>
        </Grid>
        <Grid item>
          <Button
            color="secondary"
            variant="contained"
            className={classes.axisButton}
            onClick={() => {
              this.attemptAxisMove(axis, 0.05);
            }}
          >
            +0.05mm
          </Button>
        </Grid>
        <Grid item>
          <Button
            color="secondary"
            variant="contained"
            className={classes.axisButton}
            onClick={() => {
              this.attemptAxisMove(axis, 0.01);
            }}
          >
            +0.01mm
          </Button>
        </Grid>
        <Grid item>
          <Button disabled>{axis}</Button>
        </Grid>
        <Grid item>
          <Button
            color="secondary"
            variant="contained"
            className={classes.axisButton}
            onClick={() => {
              this.attemptAxisMove(axis, -0.01);
            }}
          >
            -0.01mm
          </Button>
        </Grid>
        <Grid item>
          <Button
            color="secondary"
            variant="contained"
            className={classes.axisButton}
            onClick={() => {
              this.attemptAxisMove(axis, -0.05);
            }}
          >
            -0.05mm
          </Button>
        </Grid>
        <Grid item>
          <Button
            color="secondary"
            variant="contained"
            className={classes.axisButton}
            onClick={() => {
              this.attemptAxisMove(axis, -0.1);
            }}
          >
            -0.1mm
          </Button>
        </Grid>
        <Grid item>
          <Button
            color="secondary"
            variant="contained"
            className={classes.axisButton}
            onClick={() => {
              this.attemptAxisMove(axis, -1.0);
            }}
          >
            -1.0mm
          </Button>
        </Grid>
        <Grid item>
          {/* <Button disabled>
            {(axis === "X" && "Right") ||
              (axis === "Y" && "Closer") ||
              (axis === "Z" && "Down")}
          </Button> */}
          <IconButton disabled>
            <ArrowDownward></ArrowDownward>
          </IconButton>
        </Grid>
      </Grid>
    );
  }
}

export default withStyles(styles)(Tutorial);
