import * as THREE from "three";
import React, { Component } from "react";
import "./style.css";

import STLLoader from "../RenderingUtils/STLLoader";
import AppContext from "../../../../AppContext";
import { ScreenShare, StarTwoTone } from "@material-ui/icons";
import {
  Grid,
  Paper,
  FormGroup,
  FormControlLabel,
  FormControl,
  FormLabel,
  RadioGroup,
  Radio
} from "@material-ui/core";

const TransformControls = require('../RenderingUtils/TransformControls')(THREE);

function deg2rad(deg) {
  return deg * (Math.PI / 180);
}

class PrintRenderer extends Component {
  constructor(props) {
    super(props);

    this.state = {};
    this.canvas = React.createRef();
    this.canvasBg = null;
    this.rotationTarget = 0.0;
    this.lastTime = null;

    this.renderer = null;
    this.scene = null;
    this.control = null;
    this.cameraHelper = null;
    this.cameraPivot = null;
    this.camera = null;
    this.zTarget = 0;
    this.gridObj = null;
    this.magnetObj = null;
    this.planeObj = null;
    this.boundsObj = null;

    this.showMagnetBed = false;
    this.showBounds = false;
    this.showGrid = false;
  }

  static contextType = AppContext;

  shouldComponentUpdate(nextProps, nextState) {
    console.log("test");
  }

  componentDidMount() {
    this.assignCanvas();
  }

  // GETING DATA FORM SELECT ELEMENT TO SET POSITION SCALE OR ROTATION IN TRANSFORMCONTROL
  posRotScale = (event) => {
      switch (event.target.value) {

        case "position":
          this.control.setMode( "translate" );
          break;

        case "rotation":
          this.control.setMode( "rotate" );
          break;

        case "scale":
          this.control.setMode( "scale" );
          break;

        default:;

    }
  };

  assignCanvas() {
    // this.canvas = canvas;
    let canvasBg = this.canvas.current.parentElement;
    this.canvas.current.width = canvasBg.clientWidth;
    this.canvas.current.height = canvasBg.clientHeight;
    this.initCanvas(
      this.canvas.current,
      this.canvas.current.width,
      this.canvas.current.height
    );
  }

  initCanvas(canvas, width, height) {
    const scene = new THREE.Scene();
    const cameraPivot = new THREE.Group();
    const cameraHelper = new THREE.Group();
    cameraPivot.add(cameraHelper);
    scene.add(cameraPivot);
    const camera = new THREE.PerspectiveCamera(75, width / height, 0.1, 1000);

    const renderer = new THREE.WebGL1Renderer({ canvas });
    renderer.setSize(width, height);

    //Create a ground plane to display our mesh on
    const gpTexture = new THREE.TextureLoader().load("./buildplane.png");
    const gpMesh = new THREE.Mesh(
      new THREE.PlaneGeometry(800, 800, 1, 1),
      new THREE.MeshBasicMaterial({ map: gpTexture })
    );
    scene.add(gpMesh);
    this.planeObj = gpMesh;

    //Create alternate ground plane that renders the magnetic build surface
    const magTexture = new THREE.TextureLoader().load("./magnet.png");
    const magMesh = new THREE.Mesh(
      new THREE.PlaneGeometry(341.3, 341.3, 1, 1),
      new THREE.MeshBasicMaterial({ map: magTexture })
    );
    magMesh.position.y = -10;
    scene.add(magMesh);
    this.magnetObj = magMesh;
    this.magnetObj.visible = false;

    const path = new THREE.Path();
    path.moveTo(-150, -150);
    path.lineTo(150, -150);
    path.lineTo(150, 150);
    path.lineTo(-150, 150);
    path.lineTo(-150, -150);
    const pathMesh = new THREE.Line(
      new THREE.BufferGeometry().setFromPoints(path.getPoints()),
      new THREE.LineBasicMaterial({ color: 0xff0000, linewidth: 1 })
    );
    pathMesh.position.z = 0.25;
    scene.add(pathMesh);
    pathMesh.visible = false;
    this.boundsObj = pathMesh;

    // Add grid visual
    let grid = new THREE.GridHelper(300, 30, 0x555555, 0x555555);
    grid.rotation.set(Math.PI / 2, 0, 0);
    grid.position.set(0, 0, 1);
    scene.add(grid);
    grid.visible = false;
    this.gridObj = grid;

    let control = new TransformControls( camera, renderer.domElement );
    let mesh = null;
    control.enabled = true;
    control.setSize(2);

    const objRoot = new THREE.Group();
    const loader = new STLLoader();
    console.log("stl", this.context.stl);

    if(typeof this.context.stl == "string") {
      this.context.setFileName(this.context.stl);
      loader.load(`http://${window.location.hostname}:9990/${this.context.stl}`, (geo) => {
        const mat = new THREE.MeshPhongMaterial({
          color: 0xffffff,
          specular: 0x111111,
          shininess: 200
        });
        //   const mat = new THREE.MeshBasicMaterial({ color: 0x00ff00 });
        mesh = new THREE.Mesh(geo, mat);
        mesh.position.set(0, 0, 0);
        //   m.scale.set(0.05, 0.05, 0.05);
        //   m.setRotationFromEuler(new THREE.Euler(deg2rad(-90), 0, 0));
        mesh.geometry.center();
        control.attach( mesh );
        mesh.position.z = new THREE.Box3().setFromObject(mesh).getSize().z * 0.5;
        control.addEventListener( 'objectChange', function( e ) {
          mesh.position.z = new THREE.Box3().setFromObject(mesh).getSize().z * 0.5;
        });
        control.enabled = true;
        objRoot.add(mesh);
        this.context.setModifiedStl(mesh);
        scene.add( control );
      });
    } else {
      let reader = new FileReader();
      let context = this.context;
      context.setFileName(this.context.stl.name);
      reader.addEventListener( 'load', function ( event ) {

        let contents = event.target.result;
        let geometry = new STLLoader().parse( contents );
        console.log(contents);
        geometry.sourceType = "stl";
        geometry.sourceFile = contents;
        let material = new THREE.MeshPhongMaterial( { color: 0xffffff, specular: 0x111111, shininess: 200 } );

        mesh = new THREE.Mesh( geometry, material );
        mesh.name = contents.name;
        mesh.position.set(
              0, //x
              1, //y
              0  //z
          );
          mesh.rotation.set( 0, 0, 0 );
          mesh.scale.set( 1, 1, 1 );
          mesh.name = contents.name;
          mesh.castShadow = false;
          mesh.receiveShadow = false;
          mesh.geometry.center();
          control.attach( mesh );
          control.enabled = true;
          mesh.position.z = new THREE.Box3().setFromObject(mesh).getSize().z * 0.5;
          control.addEventListener( 'objectChange', function( e ) {
            mesh.position.z = new THREE.Box3().setFromObject(mesh).getSize().z * 0.5;
          });
          objRoot.add(mesh);
          context.setModifiedStl(mesh);
          scene.add( control );
      }, false );

      reader.readAsBinaryString(this.context.stl);
    }

    scene.add(objRoot);
    //control.attach( objRoot );
    scene.add( control );
    //renderer.render( scene, camera );

    const geometry = new THREE.BoxGeometry();
    const material = new THREE.MeshBasicMaterial({ color: 0x00ff00 });
    const cube = new THREE.Mesh(geometry, material);
    // scene.add(cube);
    cameraHelper.add(camera);
    camera.position.y = -300;
    camera.lookAt(0, 0, 0);
    cameraHelper.rotation.set(deg2rad(-45), 0, 0);

    scene.add(new THREE.HemisphereLight(0xffffff, 0x111533));

    this.camera = camera;
    this.renderer = renderer;
    this.scene = scene;
    this.control = control;
    this.cameraHelper = cameraHelper;
    this.cameraPivot = cameraPivot;

    this.renderFrame();
  }

  renderFrame() {
    requestAnimationFrame(this.renderFrame.bind(this));

    let camCurrent = this.cameraPivot.rotation.z;
    this.cameraPivot.rotation.z = camCurrent;
    this.cameraPivot.rotation.z = THREE.MathUtils.lerp(
      camCurrent,
      this.rotationTarget,
      0.01
    );

    let currentHeight = this.cameraPivot.position.z;
    this.cameraPivot.position.z = THREE.MathUtils.lerp(
      currentHeight,
      this.zTarget,
      0.01
    );

    this.renderer.render(this.scene, this.camera);
  }

  render() {
    let value = this.context;

    try {
      this.camera.position.y = -350 + value.camZoom * 3;
      this.rotationTarget = deg2rad(value.camRotation);
      this.zTarget = value.camZ;

      this.boundsObj.visible = value.showBounds;
      this.planeObj.visible = !value.showMagnet;
      this.magnetObj.visible = value.showMagnet;
      this.gridObj.visible = value.showGrid;
    } catch (err) {}

    console.log("test2");
    return (
      <>
        <div className="transform-panel">
          <Paper
            style={{
              width: "100%",
              height: "100%",
              textAlign: "center",
              paddingLeft: 8,
              paddingRight: 8
            }}
          >
            <FormControl component="fieldset">
              <FormLabel component="legend">Modify STL</FormLabel>
              <RadioGroup aria-label="Modify STL" name="modify" onChange={this.posRotScale} ref={value}>
                <FormControlLabel value="position" control={<Radio />} label="Position" />
                <FormControlLabel value="rotation" control={<Radio />} label="Rotation" />
                <FormControlLabel value="scale" control={<Radio />} label="Scale" />
              </RadioGroup>
            </FormControl>
          </Paper>
        </div>
        <div className="render-canvas-bg">
          <canvas className="render-canvas" ref={this.canvas}></canvas>
        </div>
      </>
    );
  }
}

export default PrintRenderer;
