import React, { useState } from "react";
import {
  Box,
  Button,
  Grid,
  Paper,
  Tab,
  Tabs,
  Typography
} from "@material-ui/core";
import {
  ArrowDownward,
  ArrowForward,
  ArrowUpward,
  ArrowBack,
  Home
} from "@material-ui/icons";
import { makeStyles } from "@material-ui/core/styles";

import "./Moving.css";

const useStyles = makeStyles({
  btnContainer: {
    marginTop: "14px",
    marginBottom: "28px",
    textAlign: "center"
  },
  centerBtns: {
    "& button": {
      margin: "0 12px"
    }
  },
  tabs: {
    "& button": {
      textTransform: "none"
    }
  }
});

const units = [0.1, 1.0, 10, 20];

const Moving = ({ position }) => {
  const classes = useStyles();

  const [unit, setUnit] = useState(1);

  let sendGcode = function (gcode) {
    fetch(`http://${window.location.hostname}:2127/api/v1/send`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        gcode: gcode
      })
    });
  };
  const homeAxis = (axis) => {
    //homing commands
    // const x = 'G28 X\nM114\n';
    // const y = 'G28 Y\nM114\n';
    // const z = 'G28 Z\nM114\n';
    fetch(`http://${window.location.hostname}:2127/api/v1/send`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        gcode: `G28 ${axis}\nM114\n`
      })
    });
  };

  const moveAxis = (axis, negative) => {
    //example move commands
    // const x = 'G91\nG0 X-10\nM114\n';
    // const y = 'G91\nG0 Y-1\nM114\n';
    // const z = 'G91\nG0 Z10\nM114\n';
    const command = `${axis}${negative ? -units[unit] : units[unit]}`;
    fetch(`http://${window.location.hostname}:2127/api/v1/send`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        gcode: `M220 S200\nG91\nG0 ${command}\nM220 S100\nM114\n`
      })
    });
  };

  return (
    <Box style={{ textAlign: "center" }}>
      <Typography variant="h4" style={{ marginTop: 15 }}>
        Movement
      </Typography>
      <Grid container justify="center" style={{ marginTop: 30 }}>
        <Grid item justifyContent="center" alignItems="center">
          <Button
            color="primary"
            variant="contained"
            onClick={() => {
              sendGcode("G28\nM240 S1\nM114\n");
            }}
          >
            Home All
          </Button>
        </Grid>
      </Grid>
      <Grid
        container
        justify="center"
        className={classes.btnContainer}
        spacing={0}
        direction="row"
      >
        <Grid item xs={4}>
          <Paper>
            <Tabs
              value={unit}
              className={classes.tabs}
              onChange={(e, val) => setUnit(val)}
            >
              {units.map((u) => (
                <Tab label={`${u}mm`} key={u} style={{ minWidth: "138px" }} />
              ))}
            </Tabs>
          </Paper>
        </Grid>
        <Grid item container xs={12} style={{ marginTop: 20 }}>
          <Grid
            container
            item
            xs={6}
            justify="center"
            alignItems="flex-start"
            spacing={0}
          >
            <Grid item xs={12} alignItems="flex-start">
              <Typography variant="body1">X</Typography>
            </Grid>
            <Grid item xs={3}>
              <Button
                item
                color="secondary"
                size="large"
                variant="contained"
                onClick={() => moveAxis("X", true)}
                style={{ margin: 3 }}
              >
                <ArrowBack />
              </Button>
            </Grid>
            <Grid item xs={3}>
              <Button
                color="secondary"
                size="large"
                variant="contained"
                onClick={() => homeAxis("X")}
                style={{ margin: 3 }}
              >
                <Home />
              </Button>
            </Grid>
            <Grid item xs={3}>
              <Button
                color="secondary"
                size="large"
                variant="contained"
                onClick={() => moveAxis("X", false)}
                style={{ margin: 3 }}
              >
                <ArrowForward />
              </Button>
            </Grid>
          </Grid>
          <Grid
            container
            item
            xs
            direction="column"
            alignItems="center"
            spacing={0}
          >
            <Grid item xs={12}>
              <Typography variant="body1">Y</Typography>
            </Grid>
            <Grid item>
              <Button
                color="secondary"
                size="large"
                variant="contained"
                onClick={() => moveAxis("Y", true)}
                style={{ margin: 3 }}
              >
                <ArrowUpward />
              </Button>
            </Grid>
            <Grid item>
              <Button
                color="secondary"
                size="large"
                variant="contained"
                onClick={() => homeAxis("Y")}
                style={{ margin: 3 }}
              >
                <Home />
              </Button>
            </Grid>
            <Grid item>
              <Button
                color="secondary"
                size="large"
                variant="contained"
                onClick={() => moveAxis("Y", false)}
                style={{ margin: 3 }}
              >
                <ArrowDownward />
              </Button>
            </Grid>
          </Grid>
          <Grid
            container
            item
            xs
            direction="column"
            alignItems="center"
            spacing={0}
          >
            <Grid item xs={12}>
              <Typography variant="body1">Z</Typography>
            </Grid>
            <Grid item>
              <Button
                color="secondary"
                size="large"
                variant="contained"
                onClick={() => moveAxis("Z", false)}
                style={{ margin: 3 }}
              >
                <ArrowUpward />
              </Button>
            </Grid>
            <Grid item>
              <Button
                color="secondary"
                size="large"
                variant="contained"
                onClick={() => homeAxis("Z")}
                style={{ margin: 3 }}
              >
                <Home />
              </Button>
            </Grid>
            <Grid item>
              <Button
                color="secondary"
                size="large"
                variant="contained"
                onClick={() => moveAxis("Z", true)}
                style={{ margin: 3 }}
              >
                <ArrowDownward />
              </Button>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </Box>
  );
};

export default Moving;
