import { Card, CardContent, Typography } from "@material-ui/core";
import { withStyles } from "@material-ui/core/styles";
import React, { Component } from "react";
import TutorialDefinitions from "../TutorialDefinitions";
import Tutorial from "./Tutorial";

const styles = (theme) => ({
  tutorialContainer: {
    maxHeight: "950px",
    overflow: "auto"
  },
  tutorialCard: {
    backgroundColor: "rgb(46,46,46)",
    marginBottom: "8px"
  },
  updateContainer: {
    flex: 3,
    "& h3": {
      fontSize: "28px",
      textAlign: "center"
    }
  }
});

class TutorialButton extends Component {
  constructor(props) {
    super(props);
    this.state = { tutorialShown: false };
  }

  render() {
    const { classes } = this.props;
    return (
      <>
        <Card
          className={classes.tutorialCard}
          onClick={() => {
            this.setState({ tutorialShown: true });
          }}
        >
          <CardContent>
            <Typography variant="h6">
              {TutorialDefinitions[this.props.tutorialName].name}
            </Typography>
            <Typography variant="body2">
              {TutorialDefinitions[this.props.tutorialName].description}
            </Typography>
          </CardContent>
        </Card>
        {this.state.tutorialShown && (
          <Tutorial
            tutorialName={this.props.tutorialName}
            extruderTemp={this.props.extruderTemp}
            bedTemp={this.props.bedTemp}
            showClose={true}
            onTutorialClose={(reason) => {
              this.setState({ tutorialShown: false });
            }}
          ></Tutorial>
        )}
      </>
    );
  }
}

export default withStyles(styles)(TutorialButton);
