import React, { useState, useMemo, useRef } from "react";
import CssBaseline from "@material-ui/core/CssBaseline";
import Setup from "./components/Setup/Setup";
import Home from "./components/Home/Home";
import {
  createMuiTheme,
  MuiThemeProvider
  // makeStyles,
} from "@material-ui/core/styles";
import { AppBar, Box, Typography } from "@material-ui/core";
import { getStorage, setStorage } from "./Utility/helpers";
import { useMountEffect } from "./Utility/useMountEffect";
import useWebSocket, { ReadyState } from "react-use-websocket";
import TempContext from "./TempContext";
import imgPrint from "./img/mm-print.svg";

const theme = createMuiTheme({
  palette: {
    primary: {
      main: "#0071CE"
    },
    secondary: {
      main: "#CD6930"
    },
    background: {
      default: "#3A3A3A"
    },
    type: "dark"
  },
  overrides: {
    MuiTab: {
      "root": {
        "&$selected": {
          "color": "#FFFFFF",
          "background-color": "#0055a1"
        },
        "font-weight": 300
      }
    },
    MuiAppBar: {
      "root": {
        "padding": "10px"
      }
    },
    MuiPaper: {
      rounded: {
        "border-radius": "15px"
      }
    },
    MuiButton: {
      "root": {
        "font-weight": 300
      }
    },
    MuiButtonBase: {
      "root": {
        "font-weight": 300
      }
    },
    MuiIconButton: {
      "root": {
        "font-weight": 300
      }
    },
    MuiListItemText: {
      "root": {
        "font-weight": 300
      }
    },
    MuiTypography: {
      "root": {
        "font-weight": 300
      },
      h1: {
        "font-weight": 300
      },
      h2: {
        "font-weight": 300
      },
      h3: {
        "font-weight": 300
      },
      h4: {
        "font-weight": 300
      },
      h5: {
        "font-weight": 300
      },
      h6: {
        "font-weight": 300
      },
      body1: {
        "font-weight": 300
      },
      body2: {
        "font-weight": 300
      }
    }
  }
});
// const useStyles = makeStyles({});
const App = () => {
  // const classes = useStyles();

  const [mode, setActiveMode] = useState(null);
  const [onboarded, setOnboarded] = useState(false);
  const [loading, setLoading] = useState(true);
  const [bedTemp, setBedTemp] = useState(0);
  const [bedTargetTemp, setBedTargetTemp] = useState(0);
  const [nozzleTemp, setNozzleTemp] = useState(0);
  const [nozzleTargetTemp, setNozzleTargetTemp] = useState(0);
  const [machineName, setMachineName] = useState("");

  useMountEffect(() => {
    const isOnboarded = getStorage("onboarded");
    const userMode = getStorage("userMode");
    const networkType = getStorage("networkProfile");
    const printerName = getStorage("printerName");

    if (networkType === "hotspot") {
      fetch(`http://${window.location.hostname}:8990/hotspot/connect`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        }
      });
    }

    if (networkType === "wifi") {
      fetch(`http://${window.location.hostname}:8990/hotspot/disconnect`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        }
      });
    }

    if (printerName === null) {
      setStorage("printerName", "300x");
      setMachineName("300x");
    } else {
      setMachineName(printerName);
    }

    setOnboarded(isOnboarded);
    setActiveMode(userMode);
    setLoading(true);
  });

  const handleOnboarding = (val) => {
    setActiveMode(val);
    setOnboarded(true);
  };
  const [socketUrl, setSocketUrl] = useState(
    `ws://${window.location.hostname}:9000/sockjs/718/qerksnvm/websocket`
  );
  const messageHistory = useRef([]);

  const { sendMessage, lastMessage, readyState } = useWebSocket(socketUrl, {
    //Will attempt to reconnect on all close events, such as server shutting down
    shouldReconnect: (closeEvent) => true,
    reconnectAttempts: 999999, // default: 20
    reconnectInterval: 0, // default: 5000
    onOpen: () => {
      console.log("socket opened");
    },
    onError: () => {
      console.log("socket error");
    },
    onClose: () => {
      // force reconnect somehow like sending message?
      console.log("socket closed");
    },
    onMessage: (msg) => {
      // console.log("MESSAGE EVENT");
      let json = JSON.parse(msg.data.substring(1));
      if (json.length && json.length > 0) {
        if (json[0].hasOwnProperty("current") && json[0].current.temps[0]) {
          let temps = json[0].current.temps[0];
          console.log("setting temps");
          setNozzleTemp(parseInt(temps.tool0.actual));
          setBedTemp(parseInt(temps.bed.actual));
          setNozzleTargetTemp(parseInt(temps.tool0.target));
          setBedTargetTemp(parseInt(temps.bed.target));
        }
      }
      // console.log(msg);
    }
  });

  messageHistory.current = useMemo(() => {
    try {
      messageHistory.current.concat(lastMessage);
      // let json = JSON.parse(lastMessage.data.substring(1));
      // if (json[0].hasOwnProperty("current") && json[0].current.temps[0]) {
      //   if (json[0].current.temps[0]) {
      //     let temps = json[0].current.temps[0];
      //     setNozzleTemp(parseInt(temps.tool0.actual));
      //     setBedTemp(parseInt(temps.bed.actual));
      //   }
      // }
    } catch (error) {}
  }, [lastMessage]);

  const connectionStatus = {
    [ReadyState.CONNECTING]: "Connecting",
    [ReadyState.OPEN]: "Open",
    [ReadyState.CLOSING]: "Closing",
    [ReadyState.CLOSED]: "Closed",
    [ReadyState.UNINSTANTIATED]: "Uninstantiated"
  }[readyState];

  React.useEffect(() => {
    console.log(connectionStatus);
    if (connectionStatus == "Open") {
      sendMessage('{ "auth": "E7BFE47F2DBF489B9E78A3D9D042874A" }');
      sendMessage('{ "throttle": 2 }');
      sendMessage('{ "current": "state" }');
      setLoading(false);
    }
  }, [connectionStatus]);

  return (

    <MuiThemeProvider theme={theme}>
      <TempContext.Provider
        value={{
          bedTarget: bedTargetTemp,
          bed: bedTemp,
          nozzleTarget: nozzleTargetTemp,
          nozzle: nozzleTemp,
          setBedTarget: setBedTargetTemp,
          setNozzleTarget: setNozzleTargetTemp,
          setBed: setBedTemp,
          setNozzle: setNozzleTemp
        }}
      >
        <CssBaseline />
        <Box height="100vh" display="flex" flexDirection="column">
          {onboarded && (
            <AppBar position="sticky">
              <Typography variant="h6" style={{ textAlign: "center" }}>
                Printer Name: {window.location.hostname}
              </Typography>
            </AppBar>
          )}
          {loading ? (
            <>
              <img
                src={imgPrint}
                style={{
                  width: 200,
                  height: 200,
                  zIndex: 99999999,
                  position: "absolute",
                  left: "calc(50% - 100px)",
                  top: "calc(50% - 100px)"
                }}
              ></img>
              <h4
                style={{
                  width: 200,
                  zIndex: 99999999,
                  position: "absolute",
                  left: "calc(50% - 100px)",
                  top: "calc(50% + 120px)",
                  textAlign: "center"
                }}
              >
                Loading ...
              </h4>
            </>
          ) : (
            <Home
              mode={mode}
              lastMessage={lastMessage}
              setActiveMode={setActiveMode}
            />
          )}
        </Box>
      </TempContext.Provider>
    </MuiThemeProvider>
  );
};

export default App;
