import {
  Modal,
  Paper,
  Typography,
  Grid,
  Select,
  Slider,
  Switch,
  InputLabel,
  MenuItem,
  FormControl,
  FormControlLabel,
  FormGroup,
  Button
} from "@material-ui/core";
import React, { Component, useState } from "react";
import "./style.css";
import { withStyles } from "@material-ui/styles";
import imgPrint from "../../../img/mm-print.svg";
import PrintRenderer from "../3dViewer/PrintRenderer";
import NavigationPanel from "../3dViewer/NavigationPanel";
import SettingsPanel from "../3dViewer/SettingsPanel";
import AppContext from "../../../AppContext";
import STLExporter from "../3dViewer/RenderingUtils/ExportSTL";

const styles = (theme) => ({
  formControl: {
    minWidth: 250,
    "& .MuiInputLabel-root.Mui-focused": {
      color: "white"
    }
  },
  mainContainer: {
    marginTop: 20,
    width: 300
  },
  slider: {
    width: 215
  }
});

function clamp(value, min, max) {
  if (value < min) {
    return min;
  }
  if (value > max) {
    return max;
  }
  return value;
}


function setProfileSettings(slicer, type) {
  switch (type) {
    case "good":
      slicer.metadata.preferred_quality_type = "fast";
      slicer.settings.resolution.children.layer_height.value = 0.32;
      slicer.settings.speed.children.speed_print.value = 60;
      slicer.settings.speed.children.speed_print.children.speed_infill.value = 60;
      slicer.settings.speed.children.speed_print.children.speed_wall.value = 60;
      slicer.settings.speed.children.speed_print.children.speed_wall.children.speed_wall_0.value = 60;
      slicer.settings.speed.children.speed_print.children.speed_wall.children.speed_wall_x.value = 60;
      return slicer;
    case "better":
      slicer.metadata.preferred_quality_type = "normal";
      slicer.settings.resolution.children.layer_height.value = 0.16;
      slicer.settings.speed.children.speed_print.value = 50;
      slicer.settings.speed.children.speed_print.children.speed_infill.value = 60;
      slicer.settings.speed.children.speed_print.children.speed_wall.value = 30;
      slicer.settings.speed.children.speed_print.children.speed_wall.children.speed_wall_0.value = 30;
      slicer.settings.speed.children.speed_print.children.speed_wall.children.speed_wall_x.value = 60;
      return slicer;
    case "best":
      slicer.metadata.preferred_quality_type = "best";
      slicer.settings.resolution.children.layer_height.value = 0.08;
      slicer.settings.speed.children.speed_print.value = 40;
      slicer.settings.speed.children.speed_print.children.speed_infill.value = 50;
      slicer.settings.speed.children.speed_print.children.speed_wall.value = 30;
      slicer.settings.speed.children.speed_print.children.speed_wall.children.speed_wall_0.value = 30;
      slicer.settings.speed.children.speed_print.children.speed_wall.children.speed_wall_x.value = 40;
      return slicer;
    case "petg":
      slicer.settings.travel.children.retraction_enable.value = true;
      slicer.settings.speed.children.speed_print.value = 50;
      slicer.settings.speed.children.speed_print.children.speed_infill.value = 60;
      slicer.settings.speed.children.speed_print.children.speed_wall.value = 30;
      slicer.settings.speed.children.speed_print.children.speed_wall.children.speed_wall_0.value = 30;
      slicer.settings.speed.children.speed_print.children.speed_wall.children.speed_wall_x.value = 60;
      slicer.settings.material.children.default_material_print_temperature.value = 245;
      slicer.settings.material.children.material_print_temperature.value = 245;
      slicer.settings.material.children.material_print_temperature_layer_0.value = 245;
      slicer.settings.material.children.material_initial_print_temperature.value = 245;
      slicer.settings.material.children.material_final_print_temperature.value = 245;
      slicer.settings.material.children.default_material_bed_temperature.value = 70;
      slicer.settings.material.children.material_bed_temperature.value = 70;
      return slicer;
    case "tpu":
      slicer.settings.travel.children.retraction_enable.value = false;
      slicer.settings.speed.children.speed_print.value = 30;
      slicer.settings.speed.children.speed_print.children.speed_infill.value = 30;
      slicer.settings.speed.children.speed_print.children.speed_wall.value = 30;
      slicer.settings.speed.children.speed_print.children.speed_wall.children.speed_wall_0.value = 30;
      slicer.settings.speed.children.speed_print.children.speed_wall.children.speed_wall_x.value = 30;
      slicer.settings.material.children.default_material_print_temperature.value = 230;
      slicer.settings.material.children.material_print_temperature.value = 230;
      slicer.settings.material.children.material_print_temperature_layer_0.value = 230;
      slicer.settings.material.children.material_initial_print_temperature.value = 230;
      slicer.settings.material.children.material_final_print_temperature.value = 230;
      slicer.settings.material.children.default_material_bed_temperature.value = 50;
      slicer.settings.material.children.material_bed_temperature.value = 50;
      return slicer;
    case "pla":
      slicer.settings.travel.children.retraction_enable.value = true;
      slicer.settings.speed.children.speed_print.value = 50;
      slicer.settings.speed.children.speed_print.children.speed_infill.value = 60;
      slicer.settings.speed.children.speed_print.children.speed_wall.value = 30;
      slicer.settings.speed.children.speed_print.children.speed_wall.children.speed_wall_0.value = 30;
      slicer.settings.speed.children.speed_print.children.speed_wall.children.speed_wall_x.value = 60;
      slicer.settings.material.children.default_material_print_temperature.value = 220;
      slicer.settings.material.children.material_print_temperature.value = 220;
      slicer.settings.material.children.material_print_temperature_layer_0.value = 220;
      slicer.settings.material.children.material_initial_print_temperature.value = 220;
      slicer.settings.material.children.material_final_print_temperature.value = 220;
      slicer.settings.material.children.default_material_bed_temperature.value = 50;
      slicer.settings.material.children.material_bed_temperature.value = 50;
      return slicer;
    default:
      return slicer;
  }
}

function setInfillLineDistance(slicer, percent_density) {
  // infill line distance is what we really need to set
  if (percent_density === 0) {
    slicer.settings.infill.children.infill_sparse_density.children.infill_line_distance.value = 0;
    slicer.settings.infill.children.infill_sparse_density.children.infill_line_distance.default_value = 0;
  } else {
    let multiplier = setInfillMultiplier(
      slicer.settings.infill.children.infill_pattern.default_value
    );
    let infillLineWidth =
      slicer.settings.resolution.children.line_width.children.infill_line_width
        .default_value;
    let infillLineDistance =
      ((infillLineWidth * 100) / percent_density) * multiplier;
    slicer.settings.infill.children.infill_sparse_density.children.infill_line_distance.value = infillLineDistance;
    slicer.settings.infill.children.infill_sparse_density.children.infill_line_distance.default_value = infillLineDistance;
  }
  return slicer;
}

function setInfillMultiplier(infill_pattern) {
  switch (infill_pattern) {
    case "triangles":
    case "trihexagon":
    case "cubic":
    case "cubicsubdiv":
      return 3;
    case "grid":
    case "tetrahedral":
    case "quarter_cubic":
      return 2;
    default:
      return 1;
  }
}

class SliceModal extends Component {
  constructor(props) {
    super(props);

    this.state = {
      shown: props.shown || false,
      loading: false,
      filament: "pla",
      quality: "good",
      infill: 20,
      supports: false,
      vase: false,
      file: props.file,
      camZoom: 0,
      camRotation: 0,
      camZ: 0,
      showMagnet: true,
      showBounds: true,
      showGrid: true,
      stl: props.file,
      modifiedStl: null,
      fileName: "file.stl"
    };

    this.defaultSettings = {};
  }

  componentDidMount() {
    this.getSettings();
  }

  async getSettings() {
    let onboardSettings = await fetch(
      `http://${window.location.hostname}:2127/api/v1/settings`,
      {
        method: "GET"
      }
    );

    this.defaultSettings = await onboardSettings.json();
    console.log(this.defaultSettings);
  }

  static getDerivedStateFromProps(props, state) {
    return { shown: props.shown };
  }

  async setSliceAndStartPrint() {
    this.setState({ loading: true });
    let settings = this.defaultSettings;
    if (this.state.filament === "tpu") {
      settings = setProfileSettings(settings, this.state.quality);
      settings = setProfileSettings(settings, this.state.filament);
    } else {
      settings = setProfileSettings(settings, this.state.filament);
      settings = setProfileSettings(settings, this.state.quality);
    }
    settings = setInfillLineDistance(settings, this.state.infill);

    settings.settings.infill.children.infill_sparse_density.value = this.state.infill;
    settings.settings.infill.children.infill_sparse_density.default_value = this.state.infill;
    settings.settings.support.children.support_enable.value = this.state.supports;
    settings.settings.support.children.support_enable.default_value = this.state.supports;
    settings.settings.blackmagic.children.magic_spiralize.value = this.state.vase;
    settings.settings.blackmagic.children.magic_spiralize.default_value = this.state.vase;

    let data = new FormData();
    //let stlFile = fromMesh(this.state.modifiedStl, false);
    let exporter = new STLExporter();
    let stlString = exporter.parse( this.state.modifiedStl );
    let mimeType = 'application/vnd.ms-pki.stl';
    let blob = new Blob([stlString], { type: mimeType });
    let realFile = new File([blob], this.state.fileName);
    data.append("file", realFile);
    data.append("json", JSON.stringify(settings));
    let resp = await fetch(
      `http://${window.location.hostname}:2127/api/v1/files/${this.state.fileName}`,
      { method: "POST", body: data }
    );
    if (resp.status === 200) {
      //Success
      await fetch(
        `http://${window.location.hostname}:2127/api/v1/job/${this.state.fileName}`,
        {
          method: "POST",
          headers: { "Content-Type": "application/json" }
        }
      );
    } else {
      //Error message
      if (this.props.onFailure) {
        this.props.onFailure();
      }
    }
    this.setState({ loading: false });
  }

  render() {
    const { classes } = this.props;
    const sliceModal = this;

    function addRotation(deg) {
      sliceModal.setState({ camRotation: sliceModal.state.camRotation + deg })
    }

    function setZoom(zoom) {
      sliceModal.setState({ camZoom: zoom })
    }

    function addZ(z) {
      sliceModal.setState({ camZ: clamp(sliceModal.state.camZ + z, 0, 200) })
    }

    return (
      <>
        <Modal
          open={this.state.shown}
          onClose={() => {
            this.props.onClose();
            this.setState({ shown: false });
          }}
          disableBackdropClick
        >
          <div className="modal">
            <Grid container className={classes.mainContainer}>
            <Grid item>
              <AppContext.Provider
                value={{
                  camRotation: this.state.camRotation,
                  camZoom: this.state.camZoom,
                  camZ: this.state.camZ,
                  showMagnet: this.state.showMagnet,
                  showBounds: this.state.showBounds,
                  showGrid: this.state.showGrid,
                  stl: this.props.file,
                  modifiedStl: this.state.modifiedStl,
                  fileName: this.state.fileName,
                  setModifiedStl: (stl) => {
                    this.setState({ modifiedStl: stl });
                  },
                  setFileName: (name) => {
                    this.setState({ fileName: name });
                  },
                  addRotation: (deg) => {
                    addRotation(deg);
                  },
                  setZoom: (zoom) => {
                    setZoom(zoom);
                  },
                  addZ: (z) => {
                    addZ(z);
                  },
                  setShowMagnet: (val) => {
                    this.setState({ showMagnet: val });
                  },
                  setShowBounds: (val) => {
                    this.setState({ showBounds: val });
                  },
                  setShowGrid: (val) => {
                    this.setState({ showGrid: val });
                  }
                }}
              >
              <PrintRenderer />
              <NavigationPanel />
              <SettingsPanel />
              </AppContext.Provider>
            </Grid>
            <Grid item style={{ marginLeft: 10, zIndex: 2 }}>
                <Grid item>
                  <Typography
                    variant="h2"
                    style={{ fontSize: "1.5rem", marginTop: 10 }}
                  >
                    Slice Menu
                  </Typography>
                  <FormControl className={classes.formControl}>
                    <InputLabel id="fil">Filament Type</InputLabel>
                    <Select
                      labelId="fil"
                      value={this.state.filament}
                      onChange={(e) => {
                        this.setState({ filament: e.target.value });
                      }}
                    >
                      <MenuItem value="pla">PLA</MenuItem>
                      <MenuItem value="petg">PETG</MenuItem>
                      <MenuItem value="tpu">TPU</MenuItem>
                    </Select>
                  </FormControl>
                </Grid>
                <Grid item style={{ marginTop: 20 }}>
                  <FormControl className={classes.formControl}>
                    <InputLabel id="qual">Quality</InputLabel>
                    <Select
                      labelId="qual"
                      value={this.state.quality}
                      onChange={(e) => {
                        this.setState({ quality: e.target.value });
                      }}
                    >
                      <MenuItem value="good">Fastest Print</MenuItem>
                      <MenuItem value="better">Normal Quality</MenuItem>
                      <MenuItem value="best">Best Quality</MenuItem>
                    </Select>
                  </FormControl>
                </Grid>
                <Grid item style={{ marginTop: 20 }}>
                  <Typography
                    variant="h4"
                    style={{
                      fontSize: ".75rem",
                      color: "rgba(255, 255, 255, 0.7)"
                    }}
                  >
                    Infill
                  </Typography>
                  <Slider
                    value={this.state.infill}
                    onChange={(e, newVal) => {
                      this.setState({ infill: newVal });
                    }}
                    min={0}
                    max={100}
                    step={5}
                    className={classes.slider}
                    valueLabelDisplay="auto"
                    color="secondary"
                  />
                  <Typography
                    display="inline"
                    style={{
                      fontSize: ".75rem",
                      position: "relative",
                      top: -16,
                      left: 10
                    }}
                  >{`${this.state.infill}%`}</Typography>
                </Grid>
                <Grid item style={{ marginTop: 20 }}>
                  <FormGroup row>
                    <FormControlLabel
                      control={
                        <Switch
                          checked={this.state.supports}
                          onChange={(e) => {
                            this.setState({ supports: e.target.checked });
                          }}
                          name="checkSupports"
                        />
                      }
                      label="Supports"
                    />
                    <FormControlLabel
                      control={
                        <Switch
                          checked={this.state.vase}
                          onChange={(e) => {
                            this.setState({ vase: e.target.checked });
                          }}
                          name="checkVase"
                        />
                      }
                      label="Vase Mode"
                    />
                  </FormGroup>
                </Grid>
                <Grid item container style={{ marginTop: 20 }}>
                  <Grid item xs={3}>
                    <Button
                      variant="contained"
                      color="secondary"
                      onClick={() => {
                        this.props.onClose();
                        this.setState({ shown: false });
                      }}
                    >
                      Cancel
                    </Button>
                  </Grid>
                  <Grid item xs={6} />
                  <Grid item xs={3}>
                    <Button
                      variant="contained"
                      color="primary"
                      onClick={async () => {
                        this.props.onClose();
                        await this.setSliceAndStartPrint();
                      }}
                    >
                      Slice
                    </Button>
                  </Grid>
                </Grid>
            </Grid>
            </Grid>
          </div>
        </Modal>
        {this.state.loading && (
          <>
            <img
              alt="print"
              src={imgPrint}
              style={{
                width: 200,
                height: 200,
                zIndex: 99999999,
                position: "absolute",
                left: "calc(50% - 100px)",
                top: "calc(50% - 100px)"
              }}
            ></img>
            <h4
              style={{
                width: 200,
                zIndex: 99999999,
                position: "absolute",
                left: "calc(50% - 100px)",
                top: "calc(50% + 120px)",
                textAlign: "center"
              }}
            >
              Preparing File
            </h4>
          </>
        )}
      </>
    );
  }
}

export default withStyles(styles)(SliceModal);
