class GCodeBuilder {
  constructor() {
    this._actions = [];
  }

  build() {
    let retString = "";
    for (let i = 0; i < this._actions.length; i++) {
      retString += this._actions[i] + "\n";
    }
    return retString;
  }

  addCommand(command) {
    this._actions.push(command);
  }
}

function GSetZProbeOffset(offset) {
  return `M851 Z${offset}`;
}

function GStoreParams() {
  return `M500`;
}

function GLoadParams() {
  return `M501`;
}

function Home() {
  return `G28`;
}

function BlLevel() {
  return `G29 T`;
}

/**
 *
 * @param {Boolean} x Home the X axis?
 * @param {Boolean} y Home the Y axis?
 * @param {Boolean} z Home the Z axis?
 */
function GMoveToOrigin(x, y, z) {
  if (x && y && z) {
    return `G28`;
  } else {
    return `G28${x ? "X " : ""}${y ? "Y " : ""}${x ? "Z " : ""}`;
  }
}

function GSetToAbsolutePositioning() {
  return "G90";
}

function GSetSoftwareEndstops(enabled) {
  if (enabled) {
    return "M211 S1";
  } else {
    return "M211 S0";
  }
}

function GLinearMove(doX, doY, doZ, xPos, yPos, zPos) {
  if (doX || doY || doZ) {
    return `G1${doX ? ` X${xPos}` : ""}${doY ? ` Y${yPos}` : ""}${
      doZ ? ` Z${zPos}` : ""
    }`;
  } else {
    return "";
  }
}

function GSetExtruderTemp(temp) {
  return `M104 S${temp}`;
}

function GSetBedTemp(temp) {
  return `M140 S${temp}`;
}

export {
  GCodeBuilder,
  GSetZProbeOffset,
  GStoreParams,
  GLoadParams,
  GMoveToOrigin,
  GSetToAbsolutePositioning,
  GSetSoftwareEndstops,
  Home,
  BlLevel,
  GLinearMove,
  GSetExtruderTemp,
  GSetBedTemp
};
