import {
  Grid,
  Paper,
  Typography,
  Slider,
  Button,
  IconButton,
  FormGroup,
  FormControlLabel,
  FormControl,
  Switch
} from "@material-ui/core";
import {
  ArrowDownward,
  ArrowUpward,
  RotateLeft,
  RotateRight,
  Settings,
  Videocam,
  ZoomIn,
  ZoomOut
} from "@material-ui/icons";
import React from "react";

import AppContext from "../../../../AppContext";

import "./style.css";

function SettingsPanel() {
  //   const [zoom, setZoom] = React.useState(50);
  return (
    <>
      <div className="settingspanel">
        <Paper
          style={{
            width: "100%",
            height: "100%",
            textAlign: "center",
            paddingLeft: 8,
            paddingRight: 8
          }}
        >
          <Settings
            style={{ color: "rgb(100,100,100)", height: 50, width: 40 }}
          />
          <br />
          <AppContext.Consumer>
            {({
              showBounds,
              setShowBounds,
              showMagnet,
              setShowMagnet,
              showGrid,
              setShowGrid
            }) => (
              <>
                <FormControl component="fieldset">
                  <FormGroup>
                    <FormControlLabel
                      control={
                        <Switch
                          checked={showMagnet}
                          onChange={(e) => {
                            setShowMagnet(e.target.checked);
                          }}
                        />
                      }
                      label="Magnetic Print Bed"
                    />
                    <FormControlLabel
                      control={
                        <Switch
                          checked={showBounds}
                          onChange={(e) => {
                            setShowBounds(e.target.checked);
                          }}
                        />
                      }
                      label="Show Printer Bounds"
                    />
                    <FormControlLabel
                      control={
                        <Switch
                          checked={showGrid}
                          onChange={(e) => {
                            setShowGrid(e.target.checked);
                          }}
                        />
                      }
                      label="Show Grid"
                    />
                  </FormGroup>
                </FormControl>
              </>
            )}
          </AppContext.Consumer>
        </Paper>
      </div>
    </>
  );
}

export default SettingsPanel;
