import NetworkSettings from "../../Setup/NetworkType/NetworkSettings";

import NetworkSelect from "../../Setup/NetworkSelect/NetworkSelect";

import WifiConnect from "../../Setup/NetworkType/WifiConnect";

function imageExists(url, callback) {
  var img = new Image();
  img.onload = function() { callback(true); };
  img.onerror = function() { callback(false); };
  img.src = url;
}

const useStyles = makeStyles({});
const Settings = () => {
  const [networkExpanded, setNetworkExpanded] = useState(false);
  const [networkType, setNetworkType] = useState("none");
  const [printerMode, setPrinterMode] = useState("");
  const [userMode, setUserMode] = useState("");
  const [displayWifi, setDisplayWifi] = useState(false);
  const [displaySSID, setDisplaySSID] = useState(false);
  const [camEnabled, setCamEnabled] = useState('disabled');
  const [rebootRequired, setRebootRequired] = useState('no');
  const [step, setStep] = useState(0);

  const [showSnack, setShowSnack] = useState(false);
  const [snackMessage, setSnackMessage] = useState("");
  const handleSnackClose = () => {
    setShowSnack(() => false);
    setSnackMessage("");
  };

  const classes = useStyles();

  useMountEffect(() => {
    setNetworkType(() => {
      const networkProfile = getStorage("networkProfile");
      return networkProfile !== null ? networkProfile : "none";
    });
    setCamEnabled(() => getStorage("camEnabled"));
    setPrinterMode(() => getStorage("printerProfile"));
    setUserMode(() => getStorage("userMode"));
  });

  const handleNetworkToggle = (value) => {
    const network = value.toLowerCase();
    setNetworkExpanded(false);
    setNetworkType(network);
    setStorage("networkProfile", network);
    setStep(0);
  };

  const setNewType = (value) => {
    const network = value.toLowerCase();
    setNetworkType(network);
  };

  const handleCameraToggle = (checked) => {
    let action = '';

    if(checked) {
      setCamEnabled('enabled');
      setStorage("camEnabled", 'enabled');
      action = 'add';
    } else {
      setCamEnabled('disabled');
      setStorage("camEnabled", 'disabled');
      action = 'remove';
    }

    if(action == 'add') {
      var imageUrl = `http://${window.location.hostname}:8090/?action=snapshot`;
      imageExists(imageUrl, function(exists) {
        if(!exists) {
          if(window.confirm('Reboot Required on initial camera setup. Continue?')) {
            setRebootRequired('yes');
            toggleCamera(action).then((data) => {
              setTimeout(() => {
                fetch(`http://${window.location.hostname}:8990/reboot`, {
                  method: "POST",
                  headers: {
                    "Content-Type": "application/json"
                  }
                });
              }, 3000);
            });
          } else {
            setCamEnabled('disabled');
          }
        }
      });
    }

    async function toggleCamera(action) {
      // Default options are marked with *
      const response = await fetch(`http://${window.location.hostname}:8990/toggle/camera`, {
        method: "POST",
        body: JSON.stringify({'action':action}),
        headers: { "Content-Type": "application/json" }
      });
      return response.json(); // parses JSON response into native JavaScript objects
    }
  };

  return (
    <>
      <Grid container direction="column" className={classes.container}>

        {displaySSID && (
          <Grid>
            Your SSID is
            <Typography color="secondary" variant="body1">
              Maker300X
            </Typography>
            Your password is
            <Typography color="secondary" variant="body1">
              makermade
            </Typography>
            {networkType === "ethernet" && (
              <Typography variant="body2">
                Don't forget to connect your cable!
              </Typography>
            )}
          </Grid>
        )}
        {displayWifi && (
          <Grid item xs>
            <WifiConnect
              setShowSnack={setShowSnack}
              setSnackMessage={setSnackMessage}
              handleSnackClose={handleSnackClose}
              settingsScreen={true}
            />
          </Grid>
        )}

        <Grid>
          <List
            subheader={<ListSubheader>User Type</ListSubheader>}
            className={classes.root}
          >
            <ListItem>
              <ListItemIcon>
                <LocalLibrary />
              </ListItemIcon>
              <ListItemText primary="Experience Level" />
              <Select
                native
                value={userMode}
                onChange={(e) => {
                  if (e.target && e.target.value) {
                    setUserMode(e.target.value);
                    setStorage("userMode", e.target.value);
                  }
                }}
                MenuProps={{
                  anchorOrigin: {
                    vertical: "bottom",
                    horizontal: "left"
                  },
                  getContentAnchorEl: null
                }}
              >
                <option value="beginner">Beginner</option>
                <option value="standard">Standard</option>
              </Select>
            </ListItem>
          </List>
        </Grid>
        <Grid>
          <List
            subheader={<ListSubheader>Printer</ListSubheader>}
            className={classes.root}
          >
            <ListItem>
              <ListItemIcon>
                <Class />
              </ListItemIcon>
              <ListItemText primary="Classroom Mode" />
              <ListItemSecondaryAction>
                <Switch
                  edge="end"
                  onChange={(e) => {
                    const checkedValue = e.target.checked
                      ? "classroom"
                      : "home";
                    setPrinterMode(() => checkedValue);
                    setStorage("printerProfile", checkedValue);
                  }}
                  checked={printerMode !== "home"}
                />
              </ListItemSecondaryAction>
            </ListItem>
            <ListItem>
              <ListItemIcon>
                <Dns />
              </ListItemIcon>
              <ListItemText
                primary={`Printer Name: ${getStorage("printerName")}`}
              />
              <ListItemSecondaryAction>
                {/*<Button
                  variant="contained"
                  color={networkExpanded ? "secondary" : "primary"}
                  onClick={() => {
                    setStep(1);
                  }}
                  size={networkExpanded ? "small" : "medium"}
                >
                  {networkExpanded ? "Select" : "Change"}
                </Button>*/}
              </ListItemSecondaryAction>
            </ListItem>
            <ListItem>
              <ListItemIcon>
                <Videocam />
              </ListItemIcon>
              <ListItemText primary="Enable Camera" />
              <ListItemSecondaryAction>
                <Switch
                  edge="end"
                  onChange={(e) => {
                    handleCameraToggle(e.target.checked);
                  }}
                  checked={camEnabled === 'enabled'}
                />
              </ListItemSecondaryAction>
            </ListItem>
          </List>
          {rebootRequired == 'yes' && (
            <Grid
              container
              direction="column"
              alignItems="center"
            >
            <Grid item>
              <CircularProgress className={classes.button} color="secondary" />
            </Grid>
            <Grid item>
              <Typography
                variant="h5"
                style={{ marginTop: 60, textAlign: "center" }}
              >
                Rebooting printer to apply changes, this will take approximately 2
                minutes.
              </Typography>
            </Grid>
            <Grid item>
              <Typography
                variant="h5"
                style={{ marginTop: 60, textAlign: "center" }}
              >
                DO NOT TURN OFF PRINTER UNTIL AFTER IT REBOOTS!
              </Typography>
            </Grid>
            </Grid>)}
        </Grid>
        <Snackbar
          open={showSnack}
          autoHideDuration={6000}
          message={snackMessage}
          onClose={handleSnackClose}
        ></Snackbar>
      </Grid>
      {step === 2 && (
        <div
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: "rgb(66,66,66)",
            zIndex: 8000,
            paddingLeft: 24,
            paddingRight: 24
          }}
        >
          <NetworkSelect
            setStep={setStep}
            setShowSnack={setShowSnack}
            setSnackMessage={setSnackMessage}
            handleSnackClose={handleSnackClose}
            setNewType={(type) => {
              setNewType(type);
            }}
          />
        </div>
      )}
      {step === 1 && (
        <div
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: "rgb(66,66,66)",
            zIndex: 8000,
            paddingLeft: 24,
            paddingRight: 24
          }}
        >
          <NetworkSettings
            setStep={setStep}
            setShowSnack={setShowSnack}
            setSnackMessage={setSnackMessage}
            handleSnackClose={handleSnackClose}
            setNewType={(type) => {
              setNewType(type);
            }}
          />
        </div>
      )}
    </>
  );
};

