import React, { useState } from "react";
import { Button, Box, Grid, Slider, Typography } from "@material-ui/core";
import "./Heating.css";
import { makeStyles } from "@material-ui/core/styles";

import TempContext from "../../../TempContext";

const useStyles = makeStyles((theme) => ({
  drawerContainer: {
    width: "100%"
  },
  header: {
    display: "flex",
    alignItems: "center"
  },
  item: {
    height: "100px",
    padding: "0 22px"
  },
  modal: {
    position: "absolute",
    left: "50%",
    top: "50%",
    transform: "translate(-50%, -50%)",
    width: "75%",
    height: "60%"
  },
  modalContainer: {
    height: "100%",
    padding: "25px",
    "& h2": {
      fontSize: "2.5rem",
      marginTop: "10px"
    }
  },
  printButton: {
    width: "120px",
    height: "40px",
    alignSelf: "center",
    marginTop: "auto",
    marginBottom: "100px"
  },
  sliceDropdown: {
    border: "1px solid #ced4da",
    borderRadius: 4,
    width: "150px",
    padding: "0 10px"
  },
  slider: {
    maxWidth: "60px",
    marginLeft: "20px",
    "& input": {
      padding: "8px",
      textAlign: "center"
    }
  },
  summary: {
    height: "56px",
    cursor: "pointer",
    "&$selected, &$selected:hover": {
      backgroundColor: theme.palette.secondary.main
    }
  },
  selected: {},
  hover: {},
  text: {
    fontSize: "1.5em",
    fontWeight: "bold"
  },
  gauges: {
    display: "flex"
  },
  nozzleSlider: {
    display: "flex",
    flexDirection: "row",
    width: 250
  },
  tempOutputs: {
    display: "flex",
    flexDirection: "column"
  }
}));

const Heating = () => {
  let sendGcode = function (gcode) {
    fetch(`http://${window.location.hostname}:2127/api/v1/send`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        gcode: gcode
      })
    });
  };

  const classes = useStyles();

  return (
    <Box style={{ textAlign: "center", marginTop: 40 }}>
      <Typography variant="h4">Heating</Typography>
      <Grid container justify="center">
        <Grid item xs={1}>
          <h4>Preheat</h4>
          <div style={{ display: "flex", flexDirection: "column" }}>
            <TempContext.Consumer>
              {({ setBedTarget, setNozzleTarget }) => (
                <>
                  <Button
                    color="primary"
                    variant="contained"
                    style={{ margin: 3 }}
                    onClick={() => {
                      sendGcode("M104 S220\nM140 S50");
                      setBedTarget(50);
                      setNozzleTarget(220);
                    }}
                  >
                    PLA
                  </Button>
                  <Button
                    color="primary"
                    variant="contained"
                    style={{ margin: 3 }}
                    onClick={() => {
                      sendGcode("M104 S245\nM140 S70");
                      setBedTarget(70);
                      setNozzleTarget(245);
                    }}
                  >
                    PETG
                  </Button>
                  <Button
                    color="primary"
                    variant="contained"
                    style={{ margin: 3 }}
                    onClick={() => {
                      sendGcode("M104 S230\nM140 S50");
                      setBedTarget(50);
                      setNozzleTarget(230);
                    }}
                  >
                    TPU
                  </Button>
                  <Button
                    color="primary"
                    variant="contained"
                    style={{ margin: 3 }}
                    onClick={() => {
                      sendGcode("M104 S0\nM140 S0");
                      setBedTarget(0);
                      setNozzleTarget(0);
                    }}
                  >
                    Cooldown
                  </Button>
                </>
              )}
            </TempContext.Consumer>
          </div>
        </Grid>
        <Grid item xs={7}>
          <h4></h4>
          <Grid container direction="column">
            <Grid item style={{ marginTop: 40 }}>
              <span>Nozzle</span>
            </Grid>
            <Grid item container justify="center">
              <Grid item xs={9}>
                <TempContext.Consumer>
                  {({ nozzleTarget, setNozzleTarget }) => (
                    <Slider
                      defaultValue={0}
                      color="secondary"
                      onChange={(e, newVal) => {
                        setNozzleTarget(newVal);
                      }}
                      onChangeCommitted={(e, newVal) => {
                        setNozzleTarget(newVal);
                        sendGcode(`M104 S${newVal}\n`);
                      }}
                      getAriaValueText={() => `${nozzleTarget}C`}
                      valueLabelDisplay="auto"
                      step={5}
                      min={0}
                      max={260}
                      style={{ maxWidth: "200px" }}
                      value={nozzleTarget}
                    />
                  )}
                </TempContext.Consumer>
              </Grid>
              <Grid item>
                <TempContext.Consumer>
                  {({ nozzle, nozzleTarget }) => (
                    <div className={classes.tempOutputs}>
                      <span
                        style={{ color: "#aaaaaa", width: 30 }}
                      >{`${nozzle}C`}</span>
                      <span
                        style={{ color: "#ffffff", width: 30 }}
                      >{`${nozzleTarget}C`}</span>
                    </div>
                  )}
                </TempContext.Consumer>
              </Grid>
            </Grid>
            <Grid item>
              <span>Bed</span>
            </Grid>
            <Grid container justify="center">
              <Grid item xs={9}>
                <TempContext.Consumer>
                  {({ bedTarget, setBedTarget }) => (
                    <Slider
                      defaultValue={0}
                      color="secondary"
                      onChange={(e, newVal) => {
                        setBedTarget(newVal);
                      }}
                      onChangeCommitted={(e, newVal) => {
                        setBedTarget(newVal);
                        sendGcode(`M140 S${newVal}\n`);
                      }}
                      getAriaValueText={() => `${bedTarget}C`}
                      valueLabelDisplay="auto"
                      step={5}
                      min={0}
                      max={100}
                      style={{ maxWidth: "200px" }}
                      value={bedTarget}
                    />
                  )}
                </TempContext.Consumer>
              </Grid>
              <Grid item>
                <TempContext.Consumer>
                  {({ bed, bedTarget }) => (
                    <div className={classes.tempOutputs}>
                      <span
                        style={{ color: "#aaaaaa", width: 30 }}
                      >{`${bed}C`}</span>
                      <span
                        style={{ color: "#ffffff", width: 30 }}
                      >{`${bedTarget}C`}</span>
                    </div>
                  )}
                </TempContext.Consumer>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </Box>
  );
};

export default Heating;
