import React from 'react';
import { makeStyles } from '@material-ui/core/styles';

import OptionCard from '../../Common/OptionCard/OptionCard';
import { setStorage } from '../../../Utility/helpers';
const useStyles = makeStyles({
  header: {
    textAlign: 'center',
    fontSize: '32px',
  },
});

const PrinterType = ({ setStep }) => {
  const classes = useStyles();
  const handleClick = (val) => {
    setStorage('printerProfile', val);
    setStep(3);
  };
  return (
    <div>
      <h2 className={classes.header}>Where will this printer be used?</h2>
      <OptionCard title="Home" onClick={() => handleClick('home')} />
      <OptionCard title="Classroom" onClick={() => handleClick('classroom')} />
    </div>
  );
};

export default PrinterType;
