import React, { useRef, useState } from "react";
import { Button, Grid, Typography, TextField } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import Keyboard from "react-simple-keyboard";
import { setStorage, getStorage } from "../../../Utility/helpers";
import "react-simple-keyboard/build/css/index.css";

const useStyles = makeStyles({
  backdrop: {
    zIndex: 10000,
    color: "#fff"
  },
  buttonContainer: {
    display: "flex",
    width: "100%",
    justifyContent: "space-around",
    marginTop: "56px"
  },
  connecting: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    "& h4": {
      marginBottom: "24px"
    }
  },
  container: {
    display: "flex",
    flexDirection: "column"
  },
  header: {
    display: "flex",
    alignItems: "center"
  },
  item: {
    padding: (props) => (props.settingsScreen ? "10px" : "30px 15px"),
    fontSize: (props) => (props.settingsScreen ? "18px" : "24px"),
    display: "flex",
    justifyContent: "space-between"
  },
  keyboard: {
    position: "fixed",
    bottom: 0,
    left: 0,
    right: 0,
    color: "#000"
  }
});

const NetworkSettings = ({
  setStep,
  setShowSnack,
  setSnackMessage,
  settingsScreen
}) => {
  const classes = useStyles();
  const [layout, setLayout] = useState("default");
  const [input, setInput] = useState("");
  const [connecting, setConnecting] = useState(false);
  const type = "configure";
  const keyboard = useRef();
  const old_name = getStorage("hostname");

  const nextSubmit = () => {
    !settingsScreen && setStep(4);
  }

  const handleSubmit = async () => {
    // TODO remove previous name via action "remove"
    console.log(input, "input");
    const host_name = { hostname: input, action: "add" };
    const lastHostname = { hostname: old_name !== null ? old_name : "makerhub", action: "remove"};
    const ssid_name = { ssid: input };

    //after, display success or failure
    setConnecting(false);
    const hostname_response = await fetch(`http://${window.location.hostname}:8990/change/hostname`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify(host_name)
    });

    hostname_response.json().then((data) => {
      console.log(data);
      if (data.status.includes("add")) {


      } else {
        setSnackMessage("Connection Failed. Try Again");
        setShowSnack(() => true);
        setConnecting(false);
        // show snack message "Failure"
      }
    });

    if(input !== old_name) {
      const remove_hostname_response = await fetch(`http://${window.location.hostname}:8990/change/hostname`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify(lastHostname)
      });

      remove_hostname_response.json().then((data) => {
        console.log(data);
        if (data.status.includes("remove")) {
          setStorage("hostname", input);
        }
      });
    }

    const ssid_name_response = await fetch(`http://${window.location.hostname}:8990/hotspot/change/ssid/name`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify(ssid_name)
    });

    ssid_name_response.json().then((data) => {
      console.log(data);
      setConnecting(false);
      if (data.status.includes("changed")) {
        setStorage("ssid", input);
        setSnackMessage("Successfully Named!");
        setShowSnack(() => true);
        !settingsScreen && setStep(4);
        //Go to next screen
        //!settingsScreen && setStep(2);
      } else {
        setSnackMessage("Connection Failed. Try Again");
        setShowSnack(() => true);
        // show snack message "Failure"
      }
    });

  };


  return (
    <Grid
      container
      item
      spacing={6}
      direction="column"
      alignItems="center"
      className={classes.container}
    >
      {type === "configure" && (
        <div>
          <Grid
            item
            spacing={6}
            direction="column"
            alignItems="center"
            style={{ paddingTop: 140 }}
          >
            <Typography variant="h4">Your Printer's Name Is:</Typography>
            <Typography variant="h4" style={{textAlign: 'center'}}>300X</Typography>
            </Grid>
          <Grid
            item
            spacing={6}
            direction="column"
            alignItems="center"
            style={{ marginTop: 200 }}
          >
            <Grid container style={{flexDirection:'row', justifyContent: 'flex-end'}}>
              {/*<Grid item xs={9}>
                <TextField
                  id="input"
                  label="Printer Name"
                  type="text"
                  color="secondary"
                  value={input}
                  onChange={onChangeInput}
                  style={{ width: "100%" }}
      />
              </Grid>*/}
              <Grid item>
                <Button
                  color="primary"
                  variant="contained"
                  className={classes.button}
                  style={{ marginTop: 10 }}
                  onClick={() => {
                      nextSubmit()
                  }}
                    >
                  Next
                </Button>
              </Grid>
            </Grid>
          </Grid>
        </div>
      )}

    </Grid>
  );
};

export default NetworkSettings;
